import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import {
	MenuItem,
	Select,
	TextField,
	makeStyles,
	FormHelperText,
} from '@material-ui/core';
import React from 'react';
import {
	TJurisdictionalCostTable,
	TJurisdictionalCostItem,
} from '../../../../../../types';
import {
	getPayAmount,
	AMOUNT_TYPES,
	PRIVATE_TRANSFER_FEE,
	DEAFAULT_AMOUNT_PLACEHOLDER,
} from '../helper';

const useStyles = makeStyles(() => ({
	amount: {
		marginTop: '10px',
	},
	error: {
		marginBottom: '10px',
	},
}));

type Props = {
	item: TJurisdictionalCostItem;
	isDisabled?: boolean;
};
export const Amount = ({ item, isDisabled }: Props) => {
	const classes = useStyles();
	const { values, handleChange, handleBlur, touched, errors } =
		useFormikContext<TJurisdictionalCostTable>();
	const amount = _get(item, 'PayAmount') || '';
	const itemKey = _get(item, 'ItemKey') || '';

	if (itemKey !== PRIVATE_TRANSFER_FEE) return <>{getPayAmount(amount)}</>;

	const itemAmountKey = `${itemKey}Amount`;
	const itemAmountTypeKey = `${itemKey}AmountType`;

	return (
		<>
			<Select
				fullWidth
				labelId={itemAmountTypeKey}
				id={itemAmountTypeKey}
				name={itemAmountTypeKey}
				value={values[itemAmountTypeKey]}
				onChange={handleChange}
				onBlur={handleBlur}
				displayEmpty
				error={touched[itemAmountTypeKey] && Boolean(errors[itemAmountTypeKey])}
				disabled={isDisabled}
			>
				{Object.entries(AMOUNT_TYPES).map(
					([key, { value, label }]: [string, { value: string; label: string }]) => (
						<MenuItem key={key} value={value}>
							{label}
						</MenuItem>
					)
				)}
			</Select>
			{touched[itemAmountTypeKey] && errors[itemAmountTypeKey] && (
				<FormHelperText error className={classes.error}>
					{errors[itemAmountTypeKey]}
				</FormHelperText>
			)}
			<TextField
				defaultValue={values[itemAmountKey]}
				fullWidth
				placeholder={
					DEAFAULT_AMOUNT_PLACEHOLDER[
						values[itemAmountTypeKey] as 'Amount' | 'Percentage'
					]
				}
				onChange={handleChange}
				onBlur={handleBlur}
				name={itemAmountKey}
				error={touched[itemAmountKey] && Boolean(errors[itemAmountKey])}
				disabled={isDisabled}
				className={classes.amount}
			/>
			{touched[itemAmountKey] && errors[itemAmountKey] && (
				<FormHelperText error>{errors[itemAmountKey]}</FormHelperText>
			)}
		</>
	);
};

export default Amount;
