import { Menu, MenuItem, IconButton } from '@material-ui/core';
import {
	bindTrigger,
	bindMenu,
	PopupState,
} from 'material-ui-popup-state/hooks';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import { useState } from 'react';
import { generatePath } from 'react-router';
import { useHistory } from 'react-router-dom';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import ReviewSellerSubmissionDialog from '../Actions/Dialog';
import { PATHS } from '../../../../../../routes/path';
import {
	PROPERTY_FORM_UPDATE_TYPE,
	PROPERTY_FORM_CONFIRMATION_MESSAGE,
} from '../config';
import { APPROVED_STATUS, IN_PROGRESS_STATUS } from '../../../config';
import graphqlSchema from '../../../../../../graphql';
import Loader from '../../../../../../ui-component/Loader';
import { useSnackBar } from '../../../../../../hooks';

type Props = {
	propertyId: string;
	popupState: PopupState;
	status: string;
	refetch: any;
};

const MenuAction = ({ popupState, propertyId, status, refetch }: Props) => {
	const [isOpen, setIsOpen] = useState<boolean>(false);
	const [type, setType] = useState<number>(0);
	const showSnackBar = useSnackBar();
	const history = useHistory();
	const formId = _get(popupState, 'popupId') || '';

	const isProcessing = status === IN_PROGRESS_STATUS;
	const isApproved = status === APPROVED_STATUS;

	const isDisable = isApproved || isProcessing;

	const handleCloseConfirmation = () => {
		setIsOpen(false);
	};

	const handleViewForm = (formId: string) => {
		const formLink = generatePath(PATHS.PROPERTY_FORM, {
			propertyId,
			formId,
		});
		history.push(formLink);
	};

	const handleOpenConfirmation = (type: number) => {
		setIsOpen(true);
		setType(type);
		popupState.setOpen(false);
	};

	const [doApprove, { loading: isApprove }] = useMutation(
		gql(graphqlSchema.approvePropertyForm),
		{
			onCompleted: () => {
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const [doRework, { loading: isRework }] = useMutation(
		gql(graphqlSchema.askReworkPropertyForm),
		{
			onCompleted: () => {
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const message = _get(PROPERTY_FORM_CONFIRMATION_MESSAGE, type, '') as string;

	const onConfirm = () => {
		setIsOpen(false);
		const variables = {
			PropertyId: propertyId,
			FormId: formId,
		};
		if (type === PROPERTY_FORM_UPDATE_TYPE.REQUEST_RESUBMISSION)
			return doRework({ variables });
		return doApprove({ variables });
	};

	if (isRework || isApprove) return <Loader />;

	return (
		<>
			<IconButton {...bindTrigger(popupState)}>
				<MoreHorizIcon />
			</IconButton>
			<Menu {...bindMenu(popupState)}>
				<MenuItem onClick={() => handleViewForm(formId)}>View Progress</MenuItem>
				<MenuItem
					onClick={() =>
						handleOpenConfirmation(PROPERTY_FORM_UPDATE_TYPE.REQUEST_RESUBMISSION)
					}
					disabled={isProcessing}
				>
					Request Resubmission
				</MenuItem>
				<MenuItem
					onClick={() => handleOpenConfirmation(PROPERTY_FORM_UPDATE_TYPE.OTHER)}
					disabled={isDisable}
				>
					Approve
				</MenuItem>
			</Menu>
			<ReviewSellerSubmissionDialog
				open={isOpen}
				onClose={handleCloseConfirmation}
				onConfirm={onConfirm}
				message={message}
			/>
		</>
	);
};

export default MenuAction;
