import {
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
	Checkbox,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';
import { EnhancedTableHeadProps, HeadCell } from '../../../../../../types';
import { TPropertyDoc } from '../types';

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
}));

type Props = EnhancedTableHeadProps<TPropertyDoc> & {
	headCells: HeadCell<TPropertyDoc>[];
};

const DocumentListHeader = (props: Props) => {
	const {
		order,
		orderBy,
		onRequestSort = () => {},
		headCells,
		onSelectAllClick,
		numSelected,
		rowCount,
	} = props;
	const classes = useStyles();

	const isSorted = (headCell: HeadCell<TPropertyDoc>): undefined | boolean =>
		orderBy && orderBy.id === headCell.id;

	return (
		<TableHead>
			<TableRow>
				<TableCell padding='checkbox' sx={{ pl: 3 }}>
					<Checkbox
						color='primary'
						indeterminate={numSelected > 0 && numSelected < rowCount}
						checked={rowCount > 0 && numSelected === rowCount}
						onChange={onSelectAllClick}
					/>
				</TableCell>
				{headCells.map((headCell: HeadCell<TPropertyDoc>) => (
					<TableCell
						key={headCell.id}
						align={headCell.numeric ? 'right' : 'left'}
						padding={headCell.disablePadding ? 'none' : 'normal'}
						sortDirection={isSorted(headCell) ? order : undefined}
					>
						<TableSortLabel
							active={isSorted(headCell)}
							direction={isSorted(headCell) ? order : 'asc'}
							onClick={() => onRequestSort(headCell)}
						>
							{headCell.label}
							{isSorted(headCell) ? (
								<span className={classes.sortSpan}>
									{order === 'desc' ? 'sorted descending' : 'sorted ascending'}
								</span>
							) : null}
						</TableSortLabel>
					</TableCell>
				))}
				<TableCell sortDirection={false} align='center' sx={{ width: '10%' }}>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default DocumentListHeader;
