import { Formik } from 'formik';
import {
	Button,
	CardActions,
	CardContent,
	CircularProgress,
	Grid,
	Stack,
	Divider,
} from '@material-ui/core';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import { gql, useMutation } from '@apollo/client';
import { useEffect } from 'react';
import SubCard from '../../../../ui-component/cards/SubCard';
import InputLabel from '../../../../ui-component/extended/Form/InputLabel';
import { TEditProperty } from '../../../../types';
import UploadFile from '../../components/UploadFile';
import { addPreviewProp, convertToFile } from '../../../../helpers';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import { useSnackBar, useUploadFiles } from '../../../../hooks';
import { getPropertyThumbnailPutUrl } from '../../../../graphql/queries';
import { validationEditSchema } from '../Validation';
import graphqlSchema from '../../../../graphql';
import { updatePropertyCache } from '../../../../dataProvider/cache';
import { gridSpacing } from '../../../../store/constant';
import ListInfo from './ListInfo';
import { ListPrice } from './ListPrice';
import Additions from './Additions';

type Props = {
	formData: Object;
	refetch: () => void;
};

const useStyles = makeStyles(theme => ({
	container: {
		paddingTop: theme.spacing(1),
	},
	downloadLink: {
		marginRight: theme.spacing(2),
	},
}));

const Form = (props: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const { formData, refetch } = props;
	const { files, onFilesChange, uploadFiles, isUploadLoading } = useUploadFiles({
		name: 'getPropertyThumbnailPutUrl',
		query: getPropertyThumbnailPutUrl,
		generateVariables: files => ({ Photos: files, PropertyId: propertyId }),
		uploadDoneCallback: refetch,
	});

	const property = _get(formData, 'getProperty', {});
	const {
		PropertyId: propertyId,
		BuyerAgentFeePct: buyerAgentFeePct,
		ListingAgentFeePct: listingAgentFeePct,
		ListingTerminationDate: listingTerminationDate = '',
		OfferDueDate: offerDueDate = '',
		AdditionalInfo: additionalInfo = '',
		ListingTermsConditions: listingTermsConditions = '',
		CalendarUrl: calendarUrl = '',
	} = property;

	const listPriceRange = _get(property, 'ListPriceRange', {});
	const priceMax = _get(listPriceRange, 'Max', 0);
	const priceMin = _get(listPriceRange, 'Min', 0);

	useEffect(() => {
		const propertyThumbnail = _get(property, 'Thumbnail', null);
		if (propertyThumbnail) onFilesChange([convertToFile(propertyThumbnail)]);
	}, [formData]);

	const [updatePropertyDataAction, { loading: isSaving }] = useMutation(
		gql(
			graphqlSchema.updatePropertyData({
				hasYearBuilt: false,
				hasVestingType: false,
			})
		),
		{
			update: updatePropertyCache,
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const onSubmitHandler = (values: TEditProperty) => {
		const {
			priceMax,
			priceMin,
			buyerAgentFeePct,
			listingAgentFeePct,
			listingTerminationDate,
			additionalInfo,
			listingTermsConditions,
			offerDueDate,
			calendarUrl,
		} = values;

		uploadFiles();

		const variables = {
			PropertyId: propertyId,
			ListPriceRange: { Max: priceMax, Min: priceMin },
			ListingAgentFeePct: listingAgentFeePct,
			BuyerAgentFeePct: buyerAgentFeePct,
			ListingTerminationDate: new Date(listingTerminationDate)
				.toISOString()
				.slice(0, 10),
			OfferDueDate: offerDueDate
				? new Date(offerDueDate).toISOString().slice(0, 10)
				: null,
			AdditionalInfo: additionalInfo,
			ListingTermsConditions: listingTermsConditions,
			CalendarUrl: calendarUrl || undefined,
		};
		updatePropertyDataAction({
			variables,
		});
	};
	const initialValues = {
		priceMin,
		priceMax,
		buyerAgentFeePct,
		listingAgentFeePct,
		listingTerminationDate: listingTerminationDate || new Date(),
		offerDueDate,
		additionalInfo,
		listingTermsConditions,
		calendarUrl,
	};

	return (
		<Formik
			initialValues={initialValues}
			onSubmit={onSubmitHandler}
			enableReinitialize
			validationSchema={validationEditSchema}
		>
			{({ handleSubmit }) => (
				<form noValidate onSubmit={handleSubmit}>
					<SubCard title='Listing Information'>
						<CardContent>
							<Grid container className={classes.container} spacing={gridSpacing}>
								<ListPrice />
								<ListInfo />
								<Additions />
								<Grid item xs={12}>
									<InputLabel>Property Photo</InputLabel>
									<UploadFile
										name='thumbnail'
										files={files}
										maxFiles={1}
										onDrop={files => onFilesChange(addPreviewProp(files))}
										loading={isUploadLoading}
									/>
								</Grid>
							</Grid>
						</CardContent>
						<Divider />
						<CardActions>
							<Grid item xs={12}>
								<Stack direction='row' justifyContent='flex-end'>
									<AnimateButton>
										<Button
											color='primary'
											variant='contained'
											type='submit'
											disabled={isUploadLoading || isSaving}
										>
											{isUploadLoading || isSaving ? (
												<CircularProgress size='1.5rem' color='secondary' />
											) : (
												'Save'
											)}
										</Button>
									</AnimateButton>
								</Stack>
							</Grid>
						</CardActions>
					</SubCard>
				</form>
			)}
		</Formik>
	);
};

export default Form;
