import React from 'react';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableContainer,
	TableRow,
	Alert,
	Grid,
	TableCell,
	Checkbox,
	Button,
	Collapse,
	makeStyles,
	CircularProgress,
} from '@material-ui/core';
import { usePopupState } from 'material-ui-popup-state/hooks';
import Header from './Table/Header';
import { TPropertyForm } from '../../../../../types';
import MenuAction from './Table/MenuAction';
import { useSelectRow } from '../../../../../hooks';
import { isApprovableForm } from './helper';
import { useApproveForms } from './useApproveForms';
import Loader from '../../../../../ui-component/Loader';

type Props = {
	forms: Array<TPropertyForm>;
	propertyId: string;
	refetch: any;
};

const useStyles = makeStyles({
	disabledCheckbox: {
		opacity: 0.4,
	},
});

const ReviewSellerSubmissionTable = ({ forms, propertyId, refetch }: Props) => {
	const classes = useStyles();
	const { selected, isSelected, handleSelect, handleSelectAll, clearSelected } =
		useSelectRow('id');

	const { approve, isApproving } = useApproveForms({
		propertyId,
		refetch: () => {
			refetch();
			clearSelected();
		},
	});

	const canApproveForms = forms.filter(isApprovableForm);

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, canApproveForms);
	};

	const onApprove = () => {
		const selectedForms = canApproveForms.filter(form =>
			selected.includes(form.id)
		);
		approve(selectedForms);
	};

	return (
		<>
			{isApproving && <Loader />}
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<Header
						numSelected={selected.length}
						onSelectAllClick={handleSelectAllClick}
						rowCount={canApproveForms.length}
					/>
					<TableBody>
						{forms.map(form => {
							if (typeof form === 'number') return null;
							const popupState = usePopupState({
								variant: 'popover',
								popupId: form.id,
							});
							const isItemSelected = isSelected(form.id);
							const isApprovable = isApprovableForm(form);

							return (
								<TableRow
									hover
									tabIndex={-1}
									role='checkbox'
									key={form.id}
									onClick={() => (isApprovable ? handleSelect(form.id) : '')}
									aria-checked={isItemSelected}
									selected={isItemSelected}
								>
									<TableCell sx={{ pl: 3 }} padding='checkbox'>
										<Checkbox
											classes={{ disabled: classes.disabledCheckbox }}
											color='primary'
											checked={isItemSelected}
											inputProps={{
												'aria-labelledby': `enhanced-table-checkbox-${form.id}`,
											}}
											disabled={!isApprovable}
										/>
									</TableCell>
									<TableCell align='left'>{form.formType}</TableCell>
									<TableCell align='left'>{form.status}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<MenuAction
											status={form.status}
											popupState={popupState}
											propertyId={propertyId}
											refetch={refetch}
										/>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(forms) && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			<Collapse in={!_isEmpty(selected)}>
				<Grid container item xs={12} justifyContent='flex-end' sx={{ pt: 2 }}>
					<Button
						color='primary'
						variant='contained'
						onClick={onApprove}
						disabled={isApproving}
					>
						{isApproving ? (
							<CircularProgress size='1.5rem' color='secondary' />
						) : (
							'Approve selected items'
						)}
					</Button>
				</Grid>
			</Collapse>
		</>
	);
};

export default ReviewSellerSubmissionTable;
