import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Fab, Grid, Toolbar, Tooltip } from '@material-ui/core';
import { FileDownload as FileDownloadIcon } from '@material-ui/icons';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(2),
	},
}));

type Props = {
	handleMultipleDownload: (e: React.MouseEvent<HTMLButtonElement>) => void;
	isDownloading: boolean;
};

const DownloadToolbar = ({ handleMultipleDownload, isDownloading }: Props) => {
	const classes = useToolbarStyles();

	return (
		<Toolbar className={clsx(classes.root)}>
			<Grid container alignItems='center' justifyContent='flex-end'>
				<Tooltip title='Download'>
					<Fab
						color='primary'
						aria-label='download'
						sx={{
							boxShadow: 'none',
							ml: 1,
							width: '48px',
							height: '48px',
						}}
						onClick={handleMultipleDownload}
						disabled={isDownloading}
					>
						<FileDownloadIcon />
					</Fab>
				</Tooltip>
			</Grid>
		</Toolbar>
	);
};

export default DownloadToolbar;
