import { Grid } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { reorderCategories } from '../../../../../../helpers';
import { TProperty, TOffer, TFormCategory } from '../../../../../../types';
import AgentForms from '../../AgentForms';
import Loader from '../../../../../../ui-component/Loader';
import DisclosureDocuments from '../../Documents/DisclosureDocuments';

type Props = {
	property: TProperty;
	offers: TOffer[];
	listFormCategories: { [x: string]: TFormCategory[] };
	isLoading: boolean;
};

export const Step4 = (props: Props) => {
	const { property, offers, listFormCategories, isLoading } = props;

	if (isLoading) return <Loader />;

	if (_isEmpty(offers)) return <></>;

	const propertyId = _get(property, 'PropertyId');
	const agentAvidFormDataStatuses =
		_get(property, 'AgentFdFormDataStatuses') || [];
	const formCategories = _get(listFormCategories, 'listFormCategories') || [];
	const formCategoriesSorted = reorderCategories(formCategories);

	const [offer] = offers;
	const offerId = _get(offer, 'OfferId', '');

	return (
		<>
			<Grid item lg={12}>
				<AgentForms
					title='Finance Details'
					propertyId={propertyId}
					formDataStatuses={agentAvidFormDataStatuses}
					formCategories={formCategoriesSorted}
				/>
			</Grid>
			<Grid item lg={12}>
				<DisclosureDocuments propertyId={propertyId} offerId={offerId} />
			</Grid>
		</>
	);
};
