import { Grid, makeStyles, CardContent } from '@material-ui/core';
import _get from 'lodash.get';
import { useState } from 'react';
import { gridSpacing } from '../../../store/constant';
import MainCard from '../../../ui-component/cards/MainCard';
import Status from './components/Status';
import StatusConfirmationDialog from './components/StatusConfirmationDialog';
import StepError from './components/StepError';
import { usePropertyStatus } from './components/Steps';
import { STATUS_CONFIRMATION_MESSAGES, STATUS_BACK_MESSAGES } from './config';

const useStyles = makeStyles({
	mainCard: {
		'&&': {
			padding: 0,
		},
	},
});

const PropertyActions = ({ data, refetch }: any) => {
	const classes = useStyles();
	const [open, setOpen] = useState(false);

	const property = _get(data, 'getProperty', {});

	const [dialog, setDialog] = useState<{
		message: string;
		action?: () => void;
	}>({ message: '' });

	const {
		status,
		onNext,
		onBack,
		renderContent,
		isUpdatingStatus,
		error,
		clearError,
		disabledNext,
	} = usePropertyStatus({ property, refetch });

	const onNextClick = () => {
		const nextMessage = String(
			_get(STATUS_CONFIRMATION_MESSAGES, status + 1, '')
		);
		if (nextMessage) {
			setDialog({ message: nextMessage, action: onNext });
			setOpen(true);
		} else {
			onNext();
		}
	};

	const onBackClick = () => {
		const backMessage = String(_get(STATUS_BACK_MESSAGES, status - 1, ''));

		if (backMessage) {
			setDialog({ message: backMessage, action: onBack });
			setOpen(true);
		} else {
			onBack();
		}
	};

	const onClose = () => {
		setOpen(false);
	};

	const onConfirm = () => {
		const { action } = dialog;

		if (action) action();
		setOpen(false);
	};

	return (
		<>
			<Grid container spacing={gridSpacing} alignItems='flex-start'>
				<StepError error={error} onClose={clearError} />
				<Grid item lg={4} xs={12}>
					<MainCard content={false} className={classes.mainCard}>
						<Status
							property={property}
							isUpdatePropertyStatus={isUpdatingStatus}
							status={status}
							onNextClick={onNextClick}
							onBackClick={onBackClick}
							disabledNext={disabledNext}
						/>
					</MainCard>
				</Grid>
				<Grid item lg={8} xs={12}>
					<MainCard content={false} className={classes.mainCard}>
						<CardContent>
							<Grid container spacing={gridSpacing}>
								{!isUpdatingStatus && renderContent()}
							</Grid>
						</CardContent>
					</MainCard>
				</Grid>
			</Grid>
			<StatusConfirmationDialog
				open={open}
				message={dialog.message}
				onClose={onClose}
				onConfirm={onConfirm}
			/>
		</>
	);
};

export default PropertyActions;
