import {
	TableContainer,
	Stepper,
	Step,
	StepLabel,
	Button,
	StepContent,
	Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { listPropertyStatuses } from '../../../../../graphql/queries';
import { TPropertyStatus, TPropertyActionStatus } from '../../../../../types';
import Loader from '../../../../../ui-component/Loader';
import { STEP_LIMIT, MAXIMUM_STEP, STATUS, NEXT_BUTTON } from './config';

type Props = {
	isUpdatePropertyStatus?: boolean;
	status: number;
	onBackClick: () => void;
	onNextClick: () => void;
	property: TPropertyActionStatus;
	disabledNext?: boolean;
};

const useStyles = makeStyles(theme => ({
	stepper: {
		padding: 0,
	},
	icon: {
		color: theme.palette.grey[500],
	},
	actionsContainer: {
		marginTop: theme.spacing(2),
	},
}));

const Status = ({
	isUpdatePropertyStatus,
	status,
	onBackClick,
	onNextClick,
	property,
	disabledNext,
}: Props) => {
	const classes = useStyles();
	const fullAddress = _get(property, 'PropertyData.FullAddress', '');
	const formStatuses = _get(property, 'PropertyFormDataStatuses') || [];
	const submittedForm = _isEmpty(formStatuses)
		? 0
		: formStatuses.filter((form: { FormDataSubmitted: boolean }) =>
				_get(form, 'FormDataSubmitted')
		  ).length;

	const { loading, error, data } = useQuery(gql(listPropertyStatuses));

	if (error || _isEmpty(data)) {
		return <></>;
	}

	const steps: Array<{ label: string; description: string; id: number }> = _get(
		data,
		'listPropertyStatuses',
		[]
	)
		.slice(1, STEP_LIMIT)
		.map((status: TPropertyStatus) => ({
			label: _get(status, 'Name'),
			description: _get(status, 'Desc'),
			id: _get(status, 'Id'),
		}));

	const onboardingFormStatus = `(${submittedForm}/${formStatuses.length})`;
	const isShowBackButton = status > STATUS.ONBOARDING_STEP_ID;
	const isShowNextButton = status < MAXIMUM_STEP;

	const isLoading = loading || isUpdatePropertyStatus;

	return (
		<SubCard title='Home Sale Status' sx={{ border: 0 }}>
			{isLoading && <Loader />}
			<TableContainer>
				<p>
					Current status of your property <strong>{fullAddress}</strong>
				</p>
				<Stepper
					className={classes.stepper}
					activeStep={status - 1}
					orientation='vertical'
				>
					{steps.map(({ label, description, id }) => (
						<Step key={label}>
							<StepLabel
								StepIconProps={{
									classes: {
										root: classes.icon,
									},
								}}
							>
								{label} {id === STATUS.ONBOARDING_STEP_ID ? onboardingFormStatus : ''}
							</StepLabel>
							<StepContent>
								<Typography>{description}</Typography>
								<div className={classes.actionsContainer}>
									{isShowBackButton && (
										<Button onClick={() => onBackClick()} disabled={isLoading}>
											Back
										</Button>
									)}
									{isShowNextButton && (
										<Button
											onClick={onNextClick}
											variant='contained'
											color='primary'
											disabled={isLoading || disabledNext}
										>
											{NEXT_BUTTON[status] || 'Next'}
										</Button>
									)}
								</div>
							</StepContent>
						</Step>
					))}
				</Stepper>
			</TableContainer>
		</SubCard>
	);
};

export default Status;
