import * as React from 'react';
import { TableCell, TableHead, TableRow, Checkbox } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';
import { headCells } from './helper';
import { SelectableTableHeadProps } from '../../../../../../types';

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
	cell: {
		whiteSpace: 'pre',
	},
}));

type Props = Partial<SelectableTableHeadProps>;

const Header = (props: Props) => {
	const { onSelectAllClick, numSelected = 0, rowCount = 0 } = props;
	const classes = useStyles();

	return (
		<TableHead>
			<TableRow>
				{onSelectAllClick && (
					<TableCell padding='checkbox' sx={{ pl: 3 }}>
						<Checkbox
							color='primary'
							indeterminate={numSelected > 0 && numSelected < rowCount}
							checked={rowCount > 0 && numSelected === rowCount}
							onChange={onSelectAllClick}
						/>
					</TableCell>
				)}
				{headCells.map(headCell => (
					<TableCell
						key={headCell.id}
						align='left'
						padding={headCell.disablePadding ? 'none' : 'normal'}
						className={classes.cell}
					>
						{headCell.label}
					</TableCell>
				))}
				<TableCell align='center' sx={{ pr: 3 }}>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default Header;
