import * as React from 'react';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { generatePath } from 'react-router';
import { Link } from 'react-router-dom';
import { ArrangementOrder, HeadCell, TOffer } from '../../../types';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import { getOfferInfo } from '../../../helpers';
import { PATHS } from '../../../routes/path';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

type Props = {
	offers: Array<TOffer>;
	isLoading: boolean;
	hasMore?: boolean;
	loadMore?: Function;
	propertyId?: string;
	order?: ArrangementOrder;
	orderBy?: HeadCell<TOffer> | undefined;
	onRequestSort?: (p: HeadCell<TOffer>) => void;
};

const OffersTable = ({
	offers,
	isLoading,
	hasMore,
	loadMore,
	propertyId,
	order,
	orderBy,
	onRequestSort,
}: Props) => {
	const classes = useStyles();

	const generateOfferLink = (offerId: string, offerPropertyId: string) => {
		const currentPropertyId = propertyId ?? offerPropertyId;
		return generatePath(PATHS.PROPERTY_OFFER, {
			propertyId: currentPropertyId,
			offerId,
		});
	};

	return (
		<>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header order={order} orderBy={orderBy} onRequestSort={onRequestSort} />
					<TableBody>
						{offers.map(offer => {
							if (typeof offer === 'number') return null;
							const {
								offerId,
								offerNo,
								buyerName,
								agentName,
								price,
								funding,
								loanType,
								downPayment,
								inspectionContingency,
								saleOfHomeContingency,
								financeContingency,
								appraisalContingency,
								closingPeriod,
								status,
							} = getOfferInfo(offer);
							const offerLink = generateOfferLink(offer.OfferId, offer.PropertyId);

							return (
								<TableRow hover tabIndex={-1} key={offerId}>
									<TableCell align='left'>{offerNo}</TableCell>
									<TableCell align='left'>{buyerName}</TableCell>
									<TableCell align='left'>{agentName}</TableCell>
									<TableCell align='left'>{price}</TableCell>
									<TableCell align='left'>{funding}</TableCell>
									<TableCell align='left'>{loanType}</TableCell>
									<TableCell align='left'>{downPayment}</TableCell>
									<TableCell align='left'>{financeContingency}</TableCell>
									<TableCell align='left'>{inspectionContingency}</TableCell>
									<TableCell align='left'>{appraisalContingency}</TableCell>
									<TableCell align='left'>{saleOfHomeContingency}</TableCell>
									<TableCell align='left'>{closingPeriod}</TableCell>
									<TableCell align='left'>{status}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<Link to={offerLink}>
											<IconButton color='primary'>
												<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
											</IconButton>
										</Link>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(offers) && !isLoading && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			{hasMore && loadMore && (
				<LoadMore onLoadMore={loadMore} loading={isLoading} />
			)}
		</>
	);
};

export default OffersTable;
