import { Grid } from '@material-ui/core';
import _get from 'lodash.get';
import ReviewSellerSubmission from '../../ReviewSellerSubmission';
import Loader from '../../../../../../ui-component/Loader';
import { reorderCategories } from '../../../../../../helpers';
import AgentForms from '../../AgentForms';
import OnboardingDocuments from '../../Documents/OnboardingDocuments';
import EscrowNumber from '../../EscrowNumber';

type Props = {
	isLoading: boolean;
	property: Object;
	listFormCategories: Object;
	refetch: () => void;
};

export const Step1 = (props: Props) => {
	const { isLoading, property, listFormCategories, refetch } = props;

	if (isLoading) return <Loader />;
	const propertyId = _get(property, 'PropertyId');
	const formStatuses = _get(property, 'PropertyFormDataStatuses') || [];
	const agentAvidFormDataStatuses =
		_get(property, 'AgentAvidFormDataStatuses') || [];
	const formCategories = _get(listFormCategories, 'listFormCategories') || [];
	const formCategoriesSorted = reorderCategories(formCategories);
	const escrowNumber = _get(property, 'EscrowNumber');

	return (
		<>
			<Grid item lg={12}>
				<AgentForms
					title='Agent Visual Inspection Disclosure'
					propertyId={propertyId}
					formDataStatuses={agentAvidFormDataStatuses}
					formCategories={formCategoriesSorted}
				/>
			</Grid>
			<Grid item xs={12}>
				<EscrowNumber propertyId={propertyId} escrowNumber={escrowNumber} />
			</Grid>
			<Grid item lg={12}>
				<ReviewSellerSubmission
					propertyId={propertyId}
					formStatuses={formStatuses}
					formCategories={formCategoriesSorted}
					refetch={refetch}
				/>
			</Grid>
			<Grid item lg={12}>
				<OnboardingDocuments propertyId={propertyId} />
			</Grid>
		</>
	);
};
