import _isEmpty from 'lodash.isempty';
import moment from 'moment';
import _get from 'lodash.get';
import { TOffer } from '../../../../../../types';
import { formatDate, isYes } from '../../../../../../helpers';
import { TMilestoneTracker } from './types';

const DUE_DAY_EARNEST_MONEY_DEPOSIT = 3;
const DUE_DATE_FINAL_WALKTHROUGH = 5;
const DUE_DATE_FUNDING_DATE = 3;

const getContingencyDay = (
	offer: TOffer,
	name: string,
	paUploadedDate: string
) => {
	const contingenciesDay = _get(offer, ['Offer', `${name}Days`]) || 0;
	const dueDate = getDueDate(paUploadedDate, contingenciesDay);
	return { dueDate };
};

export const mappingOfferMilestones = (
	offer: TOffer,
	paUploadedDate: string
): Array<TMilestoneTracker> => {
	if (_isEmpty(paUploadedDate)) return [];

	const closingPeriod = parseInt(_get(offer, 'Offer.ClosingPeriod') as string, 10);

	const contingencyNames = [
		{
			name: 'InspectionContingency',
			title: 'Inspection Contingency',
		},
		{
			name: 'AppraisalContingency',
			title: 'Appraisal Contingency',
		},
		{
			name: 'FinanceContingency',
			title: 'Finance Contingency',
		},
	];
	const mappingContingencies = contingencyNames.reduce(
		(
			acc: Array<TMilestoneTracker>,
			contingency: { name: string; title: string }
		) => {
			const { name, title } = contingency;

			const contingencyValue = _get(offer, ['Offer', name]) || '';

			if (!isYes(contingencyValue)) {
				return [...acc, { title }];
			}

			return [
				...acc,
				{
					title,
					...getContingencyDay(offer, name, paUploadedDate),
				},
			];
		},
		[]
	);
	return [
		{ title: 'Contract Acceptance', dueDate: formatDate(paUploadedDate) },
		{
			title: 'Earnest Money Deposit',
			dueDate: getDueDate(paUploadedDate, DUE_DAY_EARNEST_MONEY_DEPOSIT),
		},
		...mappingContingencies,
		{
			title: 'Final Walkthrough',
			dueDate: getDueDate(
				paUploadedDate,
				closingPeriod - DUE_DATE_FINAL_WALKTHROUGH
			),
		},
		{
			title: 'Funding Date',
			dueDate: getDueDate(paUploadedDate, closingPeriod - DUE_DATE_FUNDING_DATE),
		},
		{
			title: 'Close of Escrow',
			dueDate: getDueDate(paUploadedDate, closingPeriod),
		},
	];
};

export const getDueDate = (paUploadedDate: string, nextDays: number = 0) => {
	const date = moment(paUploadedDate).add(nextDays, 'days');
	return formatDate(date.format());
};
