import {
	Button,
	CardActions,
	CircularProgress,
	Grid,
	Stack,
} from '@material-ui/core';
import { Formik } from 'formik';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import {
	TProperty,
	TJurisdictionalCostItem,
	TJurisdictionalCost,
} from '../../../../../types';
import SettlementTermsTable from './Table';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import graphqlSchema from '../../../../../graphql';
import { useSnackBar } from '../../../../../hooks';
import {
	prepareInitValue,
	prepareValues,
	convertBooleanToYesNo,
	isFormListingSubmitted,
} from './helper';
import SubCard from '../../../../../ui-component/cards/SubCard';
import Hoa from './Hoa';
import { validationAddHoa } from './Validation';

type Props = {
	property: TProperty;
	refetch: any;
};

export const SettlementTermsEdit = ({ property, refetch }: Props) => {
	const showSnackBar = useSnackBar();
	const jurisdictionalCost = _get(
		property,
		'JurisdictionalCost',
		[]
	) as TJurisdictionalCost;
	const jurisdictionalCostItems = _get(
		jurisdictionalCost,
		'Items'
	) as Array<TJurisdictionalCostItem>;
	const {
		PropertyId: propertyId,
		PropertyFormDataStatuses: propertyFormDataStatuses,
	} = property;
	const initItems = prepareInitValue(jurisdictionalCostItems);
	const hoaDetectedOverride = convertBooleanToYesNo(
		_get(jurisdictionalCost, 'HOADetectedOverride')
	);
	const isDisabled =
		propertyFormDataStatuses && isFormListingSubmitted(propertyFormDataStatuses);

	const initialValues = { ...initItems, hoaDetectedOverride };

	const [updatePropertyJurisdictionalCostPayors, { loading: isSaving }] =
		useMutation(gql(graphqlSchema.updatePropertyJurisdictionalCostPayors));

	const saveData = async (values: { [key: string]: string | boolean }) => {
		try {
			const variables = prepareValues(values, jurisdictionalCostItems, propertyId);

			await updatePropertyJurisdictionalCostPayors({
				variables,
			});
			showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			refetch();
		} catch (e) {
			const message = _get(e, 'message') || 'Something went wrong';
			showSnackBar({
				message,
			});
		}
	};

	return (
		<Formik
			initialValues={initialValues}
			onSubmit={saveData}
			validationSchema={validationAddHoa}
		>
			{({ handleSubmit }) => (
				<form noValidate onSubmit={handleSubmit}>
					<SubCard title='Settlement Terms'>
						<SettlementTermsTable
							jurisdictionalCost={jurisdictionalCost}
							isDisabled={isDisabled}
						/>
						<Hoa
							isDisabled={isDisabled}
							jurisdictionalCost={jurisdictionalCost}
						/>
						<CardActions>
							<Grid item xs={12}>
								<Stack direction='row' justifyContent='flex-end'>
									<AnimateButton>
										<Button
											color='primary'
											variant='contained'
											type='submit'
											disabled={isSaving || isDisabled}
										>
											{isSaving ? (
												<CircularProgress size='1.5rem' color='secondary' />
											) : (
												'Save'
											)}
										</Button>
									</AnimateButton>
								</Stack>
							</Grid>
						</CardActions>
					</SubCard>
				</form>
			)}
		</Formik>
	);
};
