import { memo } from 'react';
import _get from 'lodash.get';
import _uniqBy from 'lodash.uniqby';
import { useFormikContext } from 'formik';
import { Autocomplete, TextField } from '@material-ui/core';
import { TDocTypeSetting, TSearchFilters, TDocSetting } from '../types';

type Props = {
	tags: TDocTypeSetting[] | TDocSetting[];
	fieldName: string;
	placeholder: string;
};

const DocumentFilter = ({ tags, fieldName, placeholder }: Props) => {
	const { values, setFieldValue } = useFormikContext<TSearchFilters>();

	return (
		<Autocomplete
			value={_get(values, [fieldName])}
			onChange={(event, newValues) => {
				setFieldValue(fieldName, _uniqBy(newValues, 'id'));
			}}
			fullWidth
			multiple
			limitTags={1}
			options={tags}
			getOptionLabel={tag => _get(tag, 'name', '')}
			defaultValue={[]}
			renderInput={params => (
				<TextField
					{...params}
					label={placeholder}
					variant='outlined'
					multiline={false}
				/>
			)}
		/>
	);
};

export default memo(DocumentFilter);
