import { useState } from 'react';
import _get from 'lodash.get';
import { gql, useLazyQuery, ApolloError } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { restProvider } from '../../../../../dataProvider';
import { useSnackBar } from '../../../../../hooks';
import { TEditDoc, TFile, TData } from './types';
import graphqlSchema from '../../../../../graphql';
import { generateFiles } from './helpers';

type Props = {
	propertyId: string;
};

const useUploadOnboardingDocs = ({ propertyId }: Props) => {
	const showSnackBar = useSnackBar();
	const [values, setValues] = useState<TEditDoc[]>([]);
	const [uploadingValue, setUploadingValue] = useState<TEditDoc | undefined>(
		undefined
	);
	const [isUploadingToS3, setIsUploadingToS3] = useState(false);

	const [doUpload, { loading }] = useLazyQuery(
		gql(graphqlSchema.getDocsUploadUrls),
		{
			onCompleted: async (data: TData) => {
				const mappingFiles = uploadingValue?.files || [];
				uploadFiles(values);
				await uploadToS3(mappingFiles, data);
			},
			onError: (error: ApolloError) => {
				showSnackBar({
					message: _get(error, 'message', 'Something went wrong'),
					variant: 'alert',
					alertSeverity: 'error',
				});
				setIsUploadingToS3(false);
			},
			fetchPolicy: 'no-cache',
		}
	);

	const uploadFiles = (newValues: TEditDoc[]) => {
		if (_isEmpty(newValues)) return;
		const uploadingValue = newValues.shift() as TEditDoc;
		setUploadingValue(uploadingValue);
		setValues(newValues);
		doUpload({
			variables: {
				PropertyId: propertyId,
				DocType: uploadingValue.docType.id || '',
				DocStatus: uploadingValue.docStatus || '',
				DocPermissions: uploadingValue.docPermissions || [],
				FilesToUpload: generateFiles(uploadingValue.files || []),
				OfferId: uploadingValue.offerId || '',
			},
		});
		setIsUploadingToS3(true);
	};

	const uploadToS3 = async (files: TFile[], data: TData) => {
		const putUrls = _get(data, 'docs_GetUploadUrls', []);
		if (_isEmpty(putUrls)) return;
		await Promise.all(
			files.map((file, index) =>
				restProvider.uploadToS3(_get(putUrls, [index, 'url'], ''), {
					body: file,
					type: file.type,
				})
			)
		);
		setIsUploadingToS3(false);
	};

	const isUploadLoading = isUploadingToS3 || loading || !_isEmpty(values);

	return {
		uploadFiles,
		isUploadLoading,
	};
};

export default useUploadOnboardingDocs;
