import { useTheme, Alert, Grid, CardContent } from '@material-ui/core';
import { useEffect } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/styles';
import Loader from '../../../ui-component/Loader';
import MainCard from '../../../ui-component/cards/MainCard';
import Form from './components/Form';
import { shouldStopPolling } from './Helper';
import { SettlementTermsEdit } from './components/SettlementTerms/Edit';
import { gridSpacing } from '../../../store/constant';

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const PropertyEdit = ({
	loading: isLoading,
	error,
	data,
	refetch,
	stopPolling,
}: any) => {
	const theme = useTheme();
	const classes = useStyles();

	const property = _get(data, 'getProperty', {});
	const isStop = shouldStopPolling(property);

	useEffect(() => {
		if (isStop) stopPolling();
	}, [isStop]);

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	const jurisdictionalCost = _get(property, 'JurisdictionalCost', []);

	return (
		<MainCard content={false} className={classes.mainCard}>
			<CardContent>
				{isLoading && <Loader />}
				{!isLoading && !_isEmpty(data) && (
					<Grid
						container
						spacing={gridSpacing}
						alignItems='flex-start'
						justifyContent='center'
					>
						<Grid item lg={6} xs={12}>
							<Form formData={data} refetch={refetch} />
						</Grid>
						{!_isEmpty(jurisdictionalCost) && (
							<Grid item lg={6} xs={12}>
								<SettlementTermsEdit property={property} refetch={refetch} />
							</Grid>
						)}
					</Grid>
				)}
			</CardContent>
		</MainCard>
	);
};

export default PropertyEdit;
