import {
	Button,
	Dialog,
	DialogContent,
	Box,
	makeStyles,
} from '@material-ui/core';

const useStyle = makeStyles({
	message: {
		whiteSpace: 'pre-line',
	},
});

type Props = {
	message: string;
	open: boolean;
	onClose: () => void;
	onConfirm: () => void;
};

const ReviewSellerSubmissionDialog = ({
	open,
	message,
	onClose,
	onConfirm,
}: Props) => {
	const classes = useStyle();
	return (
		<Dialog open={open} onClose={onClose}>
			<DialogContent sx={{ px: 3, py: 2, minWidth: { md: 600 } }}>
				<Box className={classes.message} sx={{ mb: 2 }}>
					{message}
				</Box>
				<Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
					<Button
						onClick={onClose}
						variant='text'
						sx={{ color: 'error.main', mr: 1 }}
					>
						No
					</Button>
					<Button onClick={onConfirm} variant='contained' color='primary'>
						Yes
					</Button>
				</Box>
			</DialogContent>
		</Dialog>
	);
};
export default ReviewSellerSubmissionDialog;
