import {
	Table,
	TableBody,
	TableContainer,
	Typography,
	TableRow,
	TableCell,
	TableHead,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/core/styles';
import { TOffer } from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { MILESTONE_TRACKER_STEP } from './MilestoneTracker/config';
import { gridSpacing } from '../../../../../store/constant';
import config from '../../../../../config';
import { mappingOfferMilestones } from './MilestoneTracker/helpers';
import { TMilestoneTracker } from './MilestoneTracker/types';

type Props = {
	offer: TOffer;
	paUploadedDate: string;
};

const useStyles = makeStyles(theme => ({
	root: {
		paddingBottom: theme.spacing(gridSpacing),
	},
	td: {
		paddingTop: '12px',
	},
}));

const MilestoneTracker = (props: Props) => {
	const classes = useStyles();
	const { offer, paUploadedDate } = props;
	const milestones = mappingOfferMilestones(offer, paUploadedDate);
	const fullAddress = _get(offer, ['PropertyData', 'FullAddress']);
	const stepId = _get(offer, 'StepId');
	const isHidden =
		stepId < MILESTONE_TRACKER_STEP || stepId > config.offerSteps.close;

	if (_isEmpty(milestones) || isHidden) return <></>;

	return (
		<div className={classes.root}>
			<SubCard title='Milestone Tracker'>
				<Typography variant='subtitle2'>{fullAddress}</Typography>
				<TableContainer>
					<Table
						sx={{
							'& td': {
								borderBottom: 'none',
							},
						}}
						size='small'
					>
						<TableHead>
							<TableRow>
								<TableCell>Type</TableCell>
								<TableCell>Due Date</TableCell>
							</TableRow>
						</TableHead>
						<TableBody>
							{milestones.map((milestone: TMilestoneTracker) => (
								<TableRow key={milestone.title}>
									<TableCell className={classes.td} variant='head'>
										{milestone.title}
									</TableCell>
									<TableCell className={classes.td} variant='head'>
										{milestone.dueDate || 'N/A'}
									</TableCell>
								</TableRow>
							))}
						</TableBody>
					</Table>
				</TableContainer>
			</SubCard>
		</div>
	);
};

export default MilestoneTracker;
