import { gql, useMutation } from '@apollo/client';
import { ChangeEvent, useState, useEffect } from 'react';
import _get from 'lodash.get';
import {
	Grid,
	TextField,
	Button,
	CircularProgress,
	makeStyles,
} from '@material-ui/core';
import graphqlSchema from '../../../../../graphql';
import { useSnackBar } from '../../../../../hooks';
import SubCard from '../../../../../ui-component/cards/SubCard';
import Loader from '../../../../../ui-component/Loader';
import { VALIDATE_MESSAGE } from './helpers';
import { updatePropertyCache } from '../../../../../dataProvider/cache';

const useStyles = makeStyles({
	cardContainer: {
		'& input[type=number]::-webkit-inner-spin-button, & input[type=number]::-webkit-outer-spin-button':
			{
				'-webkit-appearance': 'none',
				margin: 0,
			},
	},
});

type Props = {
	propertyId: string;
	escrowNumber: string;
};

const EscrowNumber = ({ propertyId, escrowNumber = '' }: Props) => {
	const classes = useStyles();
	const [value, setValue] = useState<string>(escrowNumber);
	const [error, setError] = useState(false);
	const showSnackBar = useSnackBar();

	const [updateProperty, { loading: isSaving }] = useMutation(
		gql(graphqlSchema.updatePropertyEscrowNumber),
		{
			update: updatePropertyCache,
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	useEffect(() => {
		if (error) setError(false);
	}, [value]);

	useEffect(() => {
		setValue(escrowNumber);
	}, [escrowNumber]);

	const handleChange = (
		ev: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
	) => {
		const {
			target: { value: newValue },
		} = ev;

		setValue(newValue);
	};

	const handleSave = () => {
		if (!value) {
			setError(true);
			return;
		}

		if (value !== escrowNumber) {
			updateProperty({
				variables: {
					PropertyId: propertyId,
					EscrowNumber: value,
				},
			});
		}
	};

	const enableSave = value === escrowNumber || !value;

	return (
		<SubCard title='Enter Escrow Number' className={classes.cardContainer}>
			{isSaving && <Loader />}
			<Grid container>
				<Grid item sm={10} xs={12}>
					<TextField
						type='number'
						value={value || ''}
						fullWidth
						placeholder='Escrow number'
						onChange={handleChange}
						name='escrowNumber'
						error={error}
						helperText={error && VALIDATE_MESSAGE}
					/>
				</Grid>
				<Grid item sm={2} xs={12} sx={{ p: 1 }}>
					<Button
						onClick={handleSave}
						variant='contained'
						color='primary'
						disabled={isSaving || enableSave}
					>
						{isSaving ? <CircularProgress size='1.5rem' color='secondary' /> : 'Save'}
					</Button>
				</Grid>
			</Grid>
		</SubCard>
	);
};

export default EscrowNumber;
