import { useState, useEffect } from 'react';
import { makeStyles, CardContent } from '@material-ui/core';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import DocumentToolbar from './DocumentToolbar';
import DocumentDialog from './DocumentDialog';
import DocumentList from './DocumentList';
import MainCard from '../../../../../ui-component/cards/MainCard';
import graphqlSchema from '../../../../../graphql';
import { TEditDoc, TPropertyDoc, TSearchFilters } from './types';
import Loader from '../../../../../ui-component/Loader';
import { useSnackBar } from '../../../../../hooks';
import useDownloadDocs from './useDownloadDocs';
import {
	mapSimpleSettings,
	mapDocTypes,
	initialDoc,
	searchDocuments,
	updateDocsCache,
} from './helpers';

type Props = {
	propertyId: string;
};

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const Documents = ({ propertyId }: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();

	const {
		data,
		loading: isLoading,
		subscribeToMore,
	} = useQuery(gql(graphqlSchema.getPropertyDocuments), {
		variables: { PropertyId: propertyId },
		skip: !propertyId,
	});

	const property = _get(data, 'getProperty') || {};
	const offers = _get(property, 'Offers') || [];
	const docs = _get(property, 'Docs') || [];
	const paUploadedDate =  _get(property, 'PAUploadedDate');
	const [validDocs, setValidDocs] = useState(docs);
	const [selectedDoc, setSelectedDoc] = useState(initialDoc);
	const [openDialog, setOpenDialog] = useState(false);
	const [isEditing, setIsEditing] = useState(false);
	const [showPatchDownload, setShowPatchDownload] = useState(false);
	const {
		data: settings,
		loading: settingsLoading,
		error: settingsError,
	} = useQuery(
		gql`
			${graphqlSchema.getDocSettings}
		`
	);

	useEffect(() => {
		if (propertyId) {
			subscribeToMore(updateDocsCache(propertyId));
		}
	}, []);

	useEffect(() => {
		setValidDocs(docs);
	}, [data]);

	const { doDownloadDocs, isDownloading } = useDownloadDocs({ propertyId });

	const handleDownloadDocs = (objectKeys: Array<string>) => {
		doDownloadDocs(objectKeys);
	};

	const rawDocTypes = _get(settings, 'docs_GetDocTypes', []);
	const docTypes = mapDocTypes(rawDocTypes);

	const rawDocPermissions = _get(settings, 'docs_GetDocPermissions', []);
	const docPermissions = mapSimpleSettings(rawDocPermissions);

	const rawDocStatuses = _get(settings, 'docs_GetDocStatuses', []);
	const docStatuses = mapSimpleSettings(rawDocStatuses);

	const handleOpenAddDialog = () => {
		setSelectedDoc(initialDoc);
		setOpenDialog(true);
	};
	const handleCloseDialog = () => {
		setIsEditing(false);
		setOpenDialog(false);
		setSelectedDoc(initialDoc);
	};
	const handleOpenEditDialog = (doc: TPropertyDoc) => {
		const validDoc: TEditDoc = {
			objectKey: doc.ObjectKey,
			offerId: doc.OfferId,
			offerNo: doc.OfferNo,
			docType: _get(docTypes, doc.DocType),
			docStatus: doc.DocStatus,
			docPermissions: doc.DocPermissions,
			files: [doc.File],
			propertyId,
		};
		setSelectedDoc(validDoc);
		setIsEditing(true);
		setOpenDialog(true);
	};

	const handleSearchFilters = (values: TSearchFilters) => {
		setValidDocs(searchDocuments(docs, values));
	};

	const handleResetSearchFilters = () => {
		setValidDocs(docs);
	};

	if (settingsLoading) return <Loader />;

	if (settingsError) {
		showSnackBar({
			message: 'Something went wrong',
			alertSeverity: 'error',
		});
		return <></>;
	}

	return (
		<>
			{(isDownloading || isLoading) && <Loader />}
			<MainCard title='' content={false} className={classes.mainCard}>
				<CardContent>
					<DocumentToolbar
						onCreate={handleOpenAddDialog}
						handleSearchFilters={handleSearchFilters}
						handleResetSearchFilters={handleResetSearchFilters}
						docStatuses={rawDocStatuses}
						docPermissions={rawDocPermissions}
						docTypes={rawDocTypes}
						showPatchDownload={showPatchDownload}
					/>
					<DocumentList
						docs={validDocs}
						handleOpenEditDialog={handleOpenEditDialog}
						handleDownloadDocs={handleDownloadDocs}
						docStatuses={docStatuses}
						docPermissions={docPermissions}
						docTypes={docTypes}
						isDownloading={isDownloading}
						isLoadingProperty={isLoading}
						showPatchDownload={showPatchDownload}
						setShowPatchDownload={setShowPatchDownload}
					/>
				</CardContent>
			</MainCard>
			<DocumentDialog
				propertyId={propertyId}
				openDialog={openDialog}
				handleCloseDialog={handleCloseDialog}
				docTypes={docTypes}
				docPermissions={docPermissions}
				docStatuses={docStatuses}
				selectedDoc={selectedDoc}
				offers={offers}
				isEditing={isEditing}
				handleDownloadDocs={handleDownloadDocs}
				isDownloading={isDownloading}
				paUploadedDate={paUploadedDate}
			/>
		</>
	);
};

export default Documents;
