import { useState } from 'react';
import _get from 'lodash.get';
import { gql, useLazyQuery, ApolloError, useMutation } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { v4 as uuidv4 } from 'uuid';
import { restProvider } from '../../../../../../dataProvider';
import { useSnackBar } from '../../../../../../hooks';
import { TEditDoc, TFile } from '../types';
import graphqlSchema from '../../../../../../graphql';
import { initialDoc, doesNeedOffer, doesNeedPAUpload } from '../helpers';

type Props = {
	propertyId: string;
	handleCloseDialog: Function;
};

type TPutUrl = { id: string; url: string };

type TData = { [key: string]: TPutUrl[] };

type TUploadDocFile = {
	name: string;
	size: string;
	type: string;
	id: string;
};

const generateFiles = (files: TFile[]): TUploadDocFile[] =>
	files.map((file: any) => {
		const { name, size, type } = file;
		const id = uuidv4();
		return {
			name: name.toString(),
			id,
			size,
			type,
		};
	});

const useUploadDocs = ({ propertyId, handleCloseDialog }: Props) => {
	const showSnackBar = useSnackBar();
	const [values, setValues] = useState<TEditDoc>(initialDoc);

	const [updateDocPermissions, { loading: isSavingPermissions }] = useMutation(
		gql(graphqlSchema.updateDocPermissions),
		{
			onCompleted: () => {
				showSnackBar({ message: 'Done', alertSeverity: 'success' });
				handleCloseDialog();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const [updatedPAUploadedDate, { loading: isSavingPAUploadedDate }] =
		useMutation(gql(graphqlSchema.updatePAUploadedDate), {
			onCompleted: () => {
				showSnackBar({ message: 'Done', alertSeverity: 'success' });
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		});

	const updatePropertyData = (values: TEditDoc) => {
		if (!doesNeedPAUpload) return;
		updatedPAUploadedDate({
			variables: {
				PropertyId: propertyId,
				PAUploadedDate: new Date(values.paUploadedDate || '')
					.toISOString()
					.slice(0, 10),
			},
		});
	};

	const [updateDoc, { loading: isSaving }] = useMutation(
		gql(graphqlSchema.updateDoc),
		{
			onCompleted: () => {
				updateDocPermissions({
					variables: {
						PropertyId: propertyId,
						ObjectKey: values.objectKey,
						DocPermissions: values.docPermissions,
					},
				});
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const [doUpload, { loading }] = useLazyQuery(
		gql(graphqlSchema.getDocsUploadUrls),
		{
			onCompleted: async (data: TData) => {
				const putUrls = _get(data, 'docs_GetUploadUrls', []);
				if (_isEmpty(putUrls)) return;
				await Promise.all(
					files.map((file, index) =>
						restProvider.uploadToS3(_get(putUrls, [index, 'url'], ''), {
							body: file,
							type: file.type,
						})
					)
				);
				setIsUploadingToS3(false);
				handleCloseDialog();
			},
			onError: (error: ApolloError) => {
				showSnackBar({
					message: _get(error, 'message', 'Something went wrong'),
					variant: 'alert',
					alertSeverity: 'error',
				});
				setIsUploadingToS3(false);
			},
		}
	);

	const [isUploadingToS3, setIsUploadingToS3] = useState(false);
	const [files, setFiles] = useState<TFile[]>([]);

	const uploadFiles = (newValues: TEditDoc) => {
		if (_isEmpty(files)) return;

		doUpload({
			variables: {
				PropertyId: propertyId,
				OfferId: doesNeedOffer(newValues.docType) ? newValues.offerId : undefined,
				DocType: newValues.docType.id || '',
				DocStatus: newValues.docStatus || '',
				DocPermissions: newValues.docPermissions || '',
				FilesToUpload: generateFiles(files),
			},
		});
		setIsUploadingToS3(true);
		updatePropertyData(newValues);
	};

	const handleUpdateDoc = (newValues: TEditDoc) => {
		setValues(newValues);
		updateDoc({
			variables: {
				PropertyId: propertyId,
				ObjectKey: newValues.objectKey,
				DocStatus: newValues.docStatus,
				DocType: newValues.docType.id,
			},
		});
		updatePropertyData(newValues);
	};

	const isUploadLoading =
		isUploadingToS3 ||
		loading ||
		isSaving ||
		isSavingPermissions ||
		isSavingPAUploadedDate;

	return {
		files,
		onFilesChange: setFiles,
		uploadFiles,
		isUploadLoading,
		handleUpdateDoc,
	};
};

export default useUploadDocs;
