import {
	Grid,
	makeStyles,
	Alert,
	useTheme,
	IconButton,
	List,
	ListItem,
	ListItemIcon,
	ListItemText,
	Typography,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import CloseIcon from '@material-ui/icons/Close';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import MainCard from '../../../../../ui-component/cards/MainCard';

const useStyles = makeStyles({
	mainCard: {
		'&&': {
			padding: 0,
		},
	},
});

type Props = {
	error: string | string[];
	onClose?: () => void;
};

export const StepError = ({ error, onClose }: Props) => {
	const classes = useStyles();
	const theme = useTheme();

	if (_isEmpty(error)) return <></>;

	const alertActions = onClose && {
		action: (
			<IconButton
				onClick={onClose}
				size='small'
				aria-label='close'
				color='inherit'
			>
				<CloseIcon fontSize='small' />
			</IconButton>
		),
	};

	return (
		<Grid item xs={12}>
			<MainCard content={false} className={classes.mainCard}>
				<Alert
					severity='error'
					sx={{ color: theme.palette.error.main }}
					{...alertActions}
				>
					{Array.isArray(error) ? (
						<List component='div' disablePadding>
							{error.map(text => (
								<ListItem key={text} sx={{ p: 0 }}>
									<ListItemIcon sx={{ minWidth: 20 }}>
										<FiberManualRecordIcon
											sx={{ fontSize: '0.5rem', color: theme.palette.error.main }}
										/>
									</ListItemIcon>
									<ListItemText primary={text} disableTypography sx={{ my: 0.25 }} />
								</ListItem>
							))}
						</List>
					) : (
						<Typography sx={{ my: 1 }}>{error}</Typography>
					)}
				</Alert>
			</MainCard>
		</Grid>
	);
};
