import _get from 'lodash.get';
import { useEffect } from 'react';
import { gql, useMutation } from '@apollo/client';
import { useFormik } from 'formik';
import { Step2 } from './Step2';
import { TUseStepHookReturn, TUseStepProps } from '../types';
import graphqlSchema from '../../../../../../graphql';
import { useSnackBar, useUploadFiles } from '../../../../../../hooks';
import { getPropertyThumbnailPutUrl } from '../../../../../../graphql/queries';
import { convertToFile } from '../../../../../../helpers';
import { updatePropertyCache } from '../../../../../../dataProvider/cache';

export type TFields = {
	offerDueDate: string;
	additionalInfo: string;
	calendarUrl: string;
};

export const useStep2 = (props: TUseStepProps): TUseStepHookReturn => {
	const showSnackBar = useSnackBar();
	const { property, refetch } = props;

	const { files, onFilesChange, uploadFiles, isUploadLoading } = useUploadFiles({
		name: 'getPropertyThumbnailPutUrl',
		query: getPropertyThumbnailPutUrl,
		generateVariables: files => ({ Photos: files, PropertyId: propertyId }),
		uploadDoneCallback: refetch,
	});

	const [updatePropertyDataAction, { loading: isSaving }] = useMutation(
		gql(graphqlSchema.updateStep2Fields),
		{
			update: updatePropertyCache,
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	useEffect(() => {
		const propertyThumbnail = _get(property, 'Thumbnail', null);
		if (propertyThumbnail) onFilesChange([convertToFile(propertyThumbnail)]);
	}, [property]);

	const {
		PropertyId: propertyId,
		OfferDueDate: offerDueDate = '',
		AdditionalInfo: additionalInfo = '',
		CalendarUrl: calendarUrl,
	} = property || {};

	const onSubmitHandler = (values: TFields) => {
		const { additionalInfo, offerDueDate, calendarUrl } = values;

		uploadFiles();

		const variables = {
			PropertyId: propertyId,
			OfferDueDate: offerDueDate
				? new Date(offerDueDate).toISOString().slice(0, 10)
				: null,
			AdditionalInfo: additionalInfo,
			CalendarUrl: calendarUrl || undefined,
		};
		updatePropertyDataAction({ variables });
	};

	const {
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		setFieldValue,
		setErrors,
	} = useFormik<TFields>({
		initialValues: { offerDueDate, additionalInfo, calendarUrl },
		enableReinitialize: true,
		onSubmit: onSubmitHandler,
	});

	const validate = () => {
		const { offerDueDate } = values;
		if (!offerDueDate) {
			setErrors({ offerDueDate: 'Please select a offer due date' });
			return false;
		}

		onSubmitHandler(values);
		return true;
	};

	const componentProps = {
		files,
		onFilesChange,
		isLoading: isUploadLoading || isSaving,
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		setFieldValue,
	};

	return {
		render: () => <Step2 {...componentProps} />,
		validate,
	};
};
