import { gql, useLazyQuery, ApolloError } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import { useSnackBar } from '../../../../../hooks';
import graphqlSchema from '../../../../../graphql';

type Props = {
	propertyId: string;
};

type TUrl = { url: string };

type TData = { [key: string]: TUrl[] };

const ZIP_FILE_NAME = 'Nile property documents';

const useDownloadDocs = ({ propertyId }: Props) => {
	const showSnackBar = useSnackBar();

	const showError = (error: Error) => {
		showSnackBar({
			message: _get(error, 'message', 'Something went wrong'),
			variant: 'alert',
			alertSeverity: 'error',
		});
	};

	const downloadZip = (files: TUrl[]) => {
		const zip = new JSZip();
		const folder = zip.folder(ZIP_FILE_NAME);

		Promise.all(
			files.map(file =>
				fetch(file.url).then(response => {
					if (response.status === 200) {
						if (folder) folder.file(_get(file, 'name', 'untitle'), response.blob());
						return;
					}
					throw new Error(response.statusText);
				})
			)
		)
			.then(() => {
				zip
					.generateAsync({ type: 'blob' })
					.then(blob => saveAs(blob, ZIP_FILE_NAME))
					.catch(error => showError(error));
			})
			.catch(error => showError(error));
	};

	const [downloadDocs, { loading: isDownloading }] = useLazyQuery(
		gql(graphqlSchema.getDocsDownloadUrls),
		{
			onCompleted: async (data: TData) => {
				const files = _get(data, 'docs_GetDownloadUrls', []);
				if (_isEmpty(files)) {
					showError(new Error('Not found'));
				}

				if (files.length === 1) {
					window.open(_get(files, [0, 'url'], ''), '_blank')?.focus();
					return;
				}

				downloadZip(files);
			},
			onError: (error: ApolloError) => showError(error),
		}
	);

	const doDownloadDocs = (objectKeys: Array<string>) => {
		downloadDocs({
			variables: {
				PropertyId: propertyId,
				ObjectKeys: objectKeys,
			},
		});
	};

	return { doDownloadDocs, isDownloading };
};

export default useDownloadDocs;
