import {
	Grid,
	Box,
	FormHelperText,
	Typography,
	Button,
	CircularProgress,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';
import { generatePath } from 'react-router';
import { Link } from 'react-router-dom';
import { underContractUploadField } from '../../Documents/helpers';
import { addPreviewProp } from '../../../../../../helpers';
import config from '../../../../../../config';
import { uploadDocumentValidator } from '../../Documents/DocumentDialog/validation';
import UploadFile from '../../../../components/UploadFile';
import PreviewDoc from '../../Documents/PreviewUploadedDoc';
import Loader from '../../../../../../ui-component/Loader';
import { TERMINATION_DATE_FORMAT } from '../../../config';
import InputLabel from '../../../../../../ui-component/extended/Form/InputLabel';
import { PATHS } from '../../../../../../routes/path';

export const Step3 = (props: any) => {
	const {
		offers,
		isLoading,
		paDocs,
		doDownloadDocs,
		files,
		onFilesChange,
		paDate,
		setPaDate,
		errors,
		onSave,
		isSaving,
		isDownloading,
		propertyId,
	} = props;

	if (isLoading) return <></>;

	if (_isEmpty(offers))
		return (
			<Grid item lg={12}>
				<p>No offer has been accepted</p>
			</Grid>
		);

	const [offer] = offers;

	const offerLink = generatePath(PATHS.PROPERTY_OFFER, {
		offerId: _get(offer, 'OfferId', ''),
		propertyId,
	});

	return (
		<Grid item container xs={12} sx={{ p: 1 }}>
			{(isSaving || isDownloading) && <Loader />}
			<Grid item container xs={12} sx={{ pb: 2 }}>
				<Typography variant='h5'>Offer No:</Typography>
				<Box component='span' sx={{ ml: 1 }}>
					<Link to={offerLink} rel='noopener noreferrer' target='_blank'>
						{offer.OfferNo}
					</Link>
				</Box>
			</Grid>
			<Grid item xs={12} sx={{ pb: 2 }}>
				<Box sx={{ mb: 2 }}>
					{(paDocs || []).map((doc: any) => (
						<PreviewDoc
							key={doc.File.name}
							file={doc.File}
							onDownload={() => doDownloadDocs(doc.ObjectKey)}
						/>
					))}
				</Box>
				<UploadFile
					title={underContractUploadField.name}
					name='docs'
					files={files || []}
					onDrop={droppedFiles => onFilesChange(addPreviewProp(droppedFiles))}
					accept={config.uploadFile.acceptedTypes}
					validator={uploadDocumentValidator}
					hasError={Boolean(errors.files)}
				/>
				<FormHelperText error={Boolean(errors.files)}>
					{errors.files}
				</FormHelperText>
			</Grid>
			<Grid item xs={12} sx={{ pb: 2 }}>
				<InputLabel>Acceptance Date</InputLabel>
				<MuiPickersUtilsProvider utils={DateFnsUtils}>
					<DatePicker
						disablePast
						format={TERMINATION_DATE_FORMAT}
						value={paDate ? new Date(paDate) : null}
						fullWidth
						placeholder='Acceptance Date'
						onChange={(date: MaterialUiPickersDate) => setPaDate(date)}
						name='acceptanceDate'
						error={Boolean(errors.paDate)}
						clearable
					/>
					<FormHelperText error={Boolean(errors.paDate)}>
						{errors.paDate}
					</FormHelperText>
				</MuiPickersUtilsProvider>
			</Grid>
			<Grid item xs={12} container justifyContent='flex-end'>
				<Button
					color='primary'
					variant='contained'
					onClick={onSave}
					disabled={isSaving}
				>
					{isSaving ? <CircularProgress size='1.5rem' color='secondary' /> : 'Save'}
				</Button>
			</Grid>
		</Grid>
	);
};
