import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Fab, Grid, Toolbar, Tooltip } from '@material-ui/core';
import { AddTwoTone as AddIcon } from '@material-ui/icons';
import DocumentSearchFilters from './DocumentSearchFilters';
import { TDocSetting, TDocTypeSetting } from './types';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(2),
	},
}));

type Props = {
	onCreate: (e: React.MouseEvent<HTMLButtonElement>) => void;
	handleSearchFilters: Function;
	handleResetSearchFilters: Function;
	docStatuses: TDocSetting[];
	docPermissions: TDocSetting[];
	docTypes: TDocTypeSetting[];
	showPatchDownload: boolean;
};

const DocumentToolbar = (props: Props) => {
	const classes = useToolbarStyles();
	const {
		onCreate,
		handleSearchFilters,
		handleResetSearchFilters,
		docStatuses,
		docPermissions,
		docTypes,
		showPatchDownload,
	} = props;

	return (
		<>
			<Toolbar className={clsx(classes.root)}>
				<DocumentSearchFilters
					handleSearchFilters={handleSearchFilters}
					handleResetSearchFilters={handleResetSearchFilters}
					docStatuses={docStatuses}
					docPermissions={docPermissions}
					docTypes={docTypes}
				/>
			</Toolbar>
			{!showPatchDownload && (
				<Toolbar className={clsx(classes.root)}>
					<Grid container alignItems='center' justifyContent='flex-end'>
						<Tooltip title='Create'>
							<Fab
								color='primary'
								aria-label='add'
								sx={{
									boxShadow: 'none',
									ml: 1,
									width: '48px',
									height: '48px',
								}}
								onClick={onCreate}
							>
								<AddIcon />
							</Fab>
						</Tooltip>
					</Grid>
				</Toolbar>
			)}
		</>
	);
};

export default DocumentToolbar;
