import {
	Grid,
	TextField,
	Button,
	CircularProgress,
	FormHelperText,
	Box,
} from '@material-ui/core';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';
import { ChangeEvent, FormEventHandler, FocusEvent } from 'react';
import { FormikTouched, FormikErrors } from 'formik';
import Loader from '../../../../../../ui-component/Loader';
import { TERMINATION_DATE_FORMAT } from '../../../config';
import InputLabel from '../../../../../../ui-component/extended/Form/InputLabel';
import UploadFile from '../../../../components/UploadFile';
import { addPreviewProp } from '../../../../../../helpers';
import { gridSpacing } from '../../../../../../store/constant';
import { TFile } from '../../../../../../types';
import { TFields } from './useStep2';

type Props = {
	files: TFile[];
	onFilesChange: (files: TFile[]) => void;
	isLoading: boolean;
	touched: FormikTouched<TFields>;
	errors: FormikErrors<TFields>;
	handleSubmit: FormEventHandler<HTMLFormElement>;
	values: TFields;
	handleChange: (event: ChangeEvent<HTMLInputElement>) => void;
	handleBlur: (event: FocusEvent<HTMLInputElement>) => void;
	setFieldValue: (field: string, value: any) => void;
};

export const Step2 = (props: Props) => {
	const {
		files,
		onFilesChange,
		isLoading,
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		setFieldValue,
	} = props;

	return (
		<Box
			component='form'
			noValidate
			onSubmit={handleSubmit}
			sx={{ pt: 3, pl: 3 }}
		>
			<Grid container spacing={gridSpacing}>
				{isLoading && <Loader />}
				<Grid item xs={12}>
					<InputLabel>Offer Due Date</InputLabel>
					<MuiPickersUtilsProvider utils={DateFnsUtils}>
						<DatePicker
							disablePast
							format={TERMINATION_DATE_FORMAT}
							value={values.offerDueDate ? new Date(values.offerDueDate) : null}
							fullWidth
							placeholder='Offer Due Date'
							onChange={(date: MaterialUiPickersDate) =>
								setFieldValue('offerDueDate', date)
							}
							name='offerDueDate'
							error={Boolean(errors.offerDueDate)}
							clearable
						/>
						<FormHelperText error={Boolean(errors.offerDueDate)}>
							{errors.offerDueDate}
						</FormHelperText>
					</MuiPickersUtilsProvider>
				</Grid>
				<Grid item xs={12}>
					<InputLabel>Calendar URL</InputLabel>
					<TextField
						id='calendarUrl'
						value={values.calendarUrl || ''}
						fullWidth
						placeholder='Calendar URL'
						onChange={handleChange}
						onBlur={handleBlur}
						error={touched.calendarUrl && Boolean(errors.calendarUrl)}
						helperText={touched.calendarUrl && errors.calendarUrl}
						name='calendarUrl'
					/>
				</Grid>
				<Grid item xs={12}>
					<InputLabel>Property Description</InputLabel>
					<TextField
						id='additionalInfo'
						value={values.additionalInfo || ''}
						fullWidth
						multiline
						rows={4}
						placeholder='Property Description'
						onChange={handleChange}
						onBlur={handleBlur}
						error={touched.additionalInfo && Boolean(errors.additionalInfo)}
						helperText={touched.additionalInfo && errors.additionalInfo}
						name='additionalInfo'
					/>
				</Grid>
				<Grid item xs={12}>
					<InputLabel>Property Photo</InputLabel>
					<UploadFile
						name='thumbnail'
						files={files}
						maxFiles={1}
						onDrop={files => onFilesChange(addPreviewProp(files))}
						loading={isLoading}
					/>
				</Grid>
				<Grid item xs={12} container justifyContent='flex-end'>
					<Button
						color='primary'
						variant='contained'
						type='submit'
						disabled={isLoading}
					>
						{isLoading ? (
							<CircularProgress size='1.5rem' color='secondary' />
						) : (
							'Save'
						)}
					</Button>
				</Grid>
			</Grid>
		</Box>
	);
};
