import { useState } from 'react';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useSnackBar } from '../../../../../hooks';
import graphqlSchema from '../../../../../graphql';
import { TPropertyForm } from '../../../../../types';

type Props = {
	propertyId: string;
	refetch: Function;
};

export const useApproveForms = ({ propertyId, refetch }: Props) => {
	const showSnackBar = useSnackBar();
	const [forms, setForms] = useState<TPropertyForm[]>([]);

	const [doApprove, { loading: isLoading }] = useMutation(
		gql(graphqlSchema.approvePropertyForm),
		{
			onCompleted: () => {
				if (_isEmpty(forms)) refetch();
				approve(forms);
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const approve = (needApproveForms: TPropertyForm[]) => {
		if (_isEmpty(needApproveForms)) return;
		const form = needApproveForms.shift() as TPropertyForm;
		setForms(needApproveForms);

		const variables = {
			PropertyId: propertyId,
			FormId: form.id,
		};
		doApprove({ variables });
	};

	const isApproving = !_isEmpty(forms) || isLoading;

	return { approve, isApproving };
};
