import { makeStyles, CardContent } from '@material-ui/core';
import { gql, useQuery } from '@apollo/client';
import _get from 'lodash.get';
import { TOffer } from '../../../../types';
import MainCard from '../../../../ui-component/cards/MainCard';
import OffersTable from '../../offer/OffersTable';
import { useOrder } from '../../../../hooks';
import {
	stableSort,
	getComparator,
	getHeadCellById,
} from '../../../../utils/table';
import { headCells } from '../../offer/helper';
import graphqlSchema from '../../../../graphql';
import Loader from '../../../../ui-component/Loader';

type Props = {
	propertyId: string;
};

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const OfferList = ({ propertyId }: Props) => {
	const classes = useStyles();
	const defaultOrder = getHeadCellById('CreatedAt', headCells);
	const { order, orderBy, handleRequestSort } = useOrder(defaultOrder);

	const { data, loading: isLoading } = useQuery(
		gql(graphqlSchema.getPropertyOffers),
		{
			variables: { PropertyId: propertyId },
			skip: !propertyId,
		}
	);

	const offers = _get(data, 'getProperty.Offers') || [];

	const sortedOffers = stableSort<TOffer>(offers, getComparator(order, orderBy));

	return (
		<>
			{isLoading && <Loader />}
			<MainCard title='' content={false} className={classes.mainCard}>
				<CardContent>
					<OffersTable
						offers={sortedOffers}
						isLoading={isLoading}
						propertyId={propertyId}
						order={order}
						orderBy={orderBy}
						onRequestSort={handleRequestSort}
					/>
				</CardContent>
			</MainCard>
		</>
	);
};

export default OfferList;
