import _get from 'lodash.get';
import { gql, useQuery } from '@apollo/client';
import { TUseStepHookReturn, TUseStepProps } from '../types';
import graphqlSchema from '../../../../../../graphql';
import { APPROVED_OFFER_STEPS } from '../../../../../../helpers';
import { Step4 } from './Step4';

export const useStep4 = (props: TUseStepProps): TUseStepHookReturn => {
	const { property } = props;
	const propertyId = _get(property, 'PropertyId');
	const { data: offersData, loading: fetchingOffers } = useQuery(
		gql(graphqlSchema.getOffers),
		{
			variables: {
				PropertyId: propertyId,
				StepIds: APPROVED_OFFER_STEPS,
			},
			skip: !propertyId,
			fetchPolicy: 'no-cache',
		}
	);

	const { data: listFormCategories, loading: fetchingForms } = useQuery(
		gql(graphqlSchema.listFormCategories)
	);

	const offers = _get(offersData, 'getOffers.Items') || [];

	const componentProps = {
		property,
		offers,
		listFormCategories,
		isLoading: fetchingOffers || fetchingForms,
	};

	return {
		render: () => <Step4 {...componentProps} />,
	};
};
