import {
	Grid,
	Typography,
	Button,
	Dialog,
	DialogActions,
	DialogContent,
	DialogTitle,
	useMediaQuery,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import { memo, useEffect } from 'react';
import { Formik } from 'formik';
import _get from 'lodash.get';
import { TEditDoc, TMappedSettings, TMappedDocTypes } from '../types';
import DocumentTypes from './DocumentTypes';
import DocumentStatuses from './DocumentStatuses';
import DocumentPermissions from './DocumentPermissions';
import DocumentOffers from './DocumentOffers';
import { gridSpacing } from '../../../../../../store/constant';
import { TOffer } from '../../../../../../types';
import Loader from '../../../../../../ui-component/Loader';
import useUploadDocs from './useUploadDocs';
import { validationEditSchema } from './validation';
import DocumentFiles from './DocumentFiles';

type Props = {
	propertyId: string;
	openDialog: boolean;
	handleCloseDialog: () => void;
	selectedDoc: TEditDoc;
	docTypes: TMappedDocTypes;
	docPermissions: TMappedSettings;
	docStatuses: TMappedSettings;
	offers: Array<TOffer>;
	isEditing: boolean;
	handleDownloadDocs: Function;
	isDownloading: boolean;
	paUploadedDate: string;
};

const DocumentDialog = ({
	propertyId,
	openDialog,
	handleCloseDialog,
	docTypes,
	docPermissions,
	docStatuses,
	selectedDoc,
	offers,
	isEditing,
	handleDownloadDocs,
	isDownloading,
	paUploadedDate,
}: Props) => {
	const theme = useTheme();
	const matchMobile = useMediaQuery(theme.breakpoints.down('sm'));
	const { files, onFilesChange, uploadFiles, isUploadLoading, handleUpdateDoc } =
		useUploadDocs({
			propertyId,
			handleCloseDialog,
		});

	useEffect(() => {
		onFilesChange([]);
	}, [openDialog]);

	const onSubmitHandler = (values: TEditDoc) => {
		if (isEditing) {
			handleUpdateDoc(values);
			return;
		}

		uploadFiles(values);
	};

	const documentFilesProps = {
		isEditing,
		isDownloading,
		handleDownloadDocs,
		objectKey: selectedDoc.objectKey,
		files,
		onFilesChange,
		isUploadLoading,
	};

	const selectedDocAndPADate = { ...selectedDoc, paUploadedDate };

	return (
		<>
			{isUploadLoading && <Loader />}
			<Dialog
				fullScreen={matchMobile}
				open={openDialog}
				aria-labelledby='dialog-title'
				fullWidth
				scroll='paper'
			>
				<DialogTitle id='dialog-title'>
					<Typography variant='inherit' sx={{ fontSize: '1rem', fontWeight: 600 }}>
						{isEditing ? _get(selectedDocAndPADate, 'files.0.name') : 'Upload new documents'}
					</Typography>
				</DialogTitle>
				<Formik
					initialValues={selectedDocAndPADate}
					onSubmit={onSubmitHandler}
					enableReinitialize
					validationSchema={validationEditSchema}
				>
					{({ handleSubmit }) => (
						<form noValidate onSubmit={handleSubmit}>
							<DialogContent sx={{ px: 3, py: 2 }}>
								<Grid
									container
									spacing={gridSpacing}
									alignItems='flex-start'
									justifyContent='space-between'
									direction='row'
								>
									<DocumentFiles {...documentFilesProps} />
									<DocumentTypes docTypes={docTypes} />
									<DocumentStatuses docStatuses={docStatuses} />
									<DocumentPermissions docPermissions={docPermissions} />
									<DocumentOffers docOffers={offers} isEditing={isEditing} />
								</Grid>
							</DialogContent>
							<DialogActions sx={{ px: 3, py: 2 }}>
								<Button
									autoFocus
									onClick={handleCloseDialog}
									disabled={isUploadLoading}
								>
									Cancel
								</Button>
								<Button
									autoFocus
									color='primary'
									variant='contained'
									type='submit'
									disabled={isUploadLoading}
								>
									Save
								</Button>
							</DialogActions>
						</form>
					)}
				</Formik>
			</Dialog>
		</>
	);
};

export default memo(DocumentDialog);
