import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	TProperty,
	TPropertyFormDataStatus,
	TSellRequest,
} from '../../../types';
import {
	APPROVED_STATUS,
	IN_PROGRESS_STATUS,
	SUBMITTED_STATUS,
	STATUS as PROPERTY_STATUS,
} from './config';
import config from '../../../config';

const READY_STATUS = 'Ready';
const ERROR_STATUS = 'Error';

export const isReady = (property: TSellRequest) => {
	const dTPropertyId = _get(property, 'DTPropertyId');
	const propertyData = _get(property, 'PropertyData');
	const taxStatus = _get(property, 'ReportStatus');
	const taxResourceStatus = _get(taxStatus, 'TaxReportTaxResourceStatus', '');
	const totalViewStatus = _get(taxStatus, 'TaxReportTotalViewStatus', '');

	return (
		!_isEmpty(dTPropertyId && propertyData) &&
		taxResourceStatus === READY_STATUS &&
		totalViewStatus === READY_STATUS
	);
};

const shouldStopPolling = (property: TSellRequest) => {
	const taxStatus = _get(property, 'ReportStatus');
	const taxResourceStatus = _get(taxStatus, 'TaxReportTaxResourceStatus', '');
	const totalViewStatus = _get(taxStatus, 'TaxReportTotalViewStatus', '');

	return (
		!isReady(property) ||
		taxResourceStatus === ERROR_STATUS ||
		totalViewStatus === ERROR_STATUS
	);
};

const extractPropertyData = (row: TProperty) => {
	const propertyData = _get(row, 'PropertyData', {});
	const streetAddress = _get(propertyData, 'StreetAddress', '');
	const county = _get(propertyData, 'County', '');
	const zip = _get(propertyData, 'Zip', '');
	const city = _get(propertyData, 'City', '');
	const state = _get(propertyData, 'State', '');
	const propertyNo = _get(row, 'PropertyNo', '');
	const createdAt = _get(row, 'CreatedAt', '');
	return { streetAddress, county, zip, city, state, propertyNo, createdAt };
};

const getFormTypeName = (
	categories: Object,
	formTypeId: string,
	categoryId: string
) => _get(categories, [categoryId, 'formTypes', formTypeId]);

const isFormSubmitted = (
	formStatuses: Array<TPropertyFormDataStatus>,
	currentFormTypeId: string
) =>
	formStatuses.some(formStatus => {
		const { FormDataSubmitted: isSubmitted, FormTypeId: formTypeId } = formStatus;
		return isSubmitted && currentFormTypeId === formTypeId;
	});

const getPropertyFormStatus = (propertyForm: TPropertyFormDataStatus) => {
	const isApproved = Boolean(_get(propertyForm, 'FormDataApproved', false));
	if (isApproved) return APPROVED_STATUS;

	const isSubmitted = Boolean(_get(propertyForm, 'FormDataSubmitted', false));
	if (isSubmitted) return SUBMITTED_STATUS;

	return IN_PROGRESS_STATUS;
};

const replaceAllDashSymbol = (text: string) => {
	if (_isEmpty(text)) return '';
	return text.replaceAll(' ', '-');
};

export const buildPropertyExternalUrl = (
	externalId: string,
	streetAddress: string
) => {
	const query = `?street=${replaceAllDashSymbol(streetAddress)}`;
	return `${config.dispatcherUrl}/${externalId}${query}`;
};

const VIEWABLE_PROPERTY_STATUSES = [
	PROPERTY_STATUS.ONBOARDING_STEP_ID,
	PROPERTY_STATUS.HOME_PREPARATION,
	PROPERTY_STATUS.ON_CONTRACT,
	PROPERTY_STATUS.PENDING,
];

export const getPropertyFilter = (query: string, statusIds?: string[]) => query !== config.defaultText.query
		? { Query: query, StatusIds: statusIds }
		: {
				Query: query,
				StatusIds: _isEmpty(statusIds) ? VIEWABLE_PROPERTY_STATUSES : statusIds,
		  };

export {
	shouldStopPolling,
	extractPropertyData,
	getFormTypeName,
	isFormSubmitted,
	getPropertyFormStatus,
	VIEWABLE_PROPERTY_STATUSES,
};
