import * as yup from 'yup';
import _get from 'lodash.get';
import type { FileError } from 'react-dropzone';
import {
	doesNeedOffer,
	doesNeedPAUpload,
	doesNeedPermission,
} from '../helpers';
import { TDocTypeSetting } from '../types';
import config from '../../../../../../config';
import { convertBytesToMib } from '../../../../../../helpers';

const messages = {
	files: 'Files are required',
	maxSize: `One of the files is too large. Allowed maximum size is ${convertBytesToMib(
		config.uploadFile.maxSize
	)} MB.`,
	type: 'Document type is required',
	status: 'Document status is required',
	permissions: 'Document permissions are required',
	offer: 'Offer is required',
	paUploadedDate: 'Date of Purchase Agreement Upload is required',
};

export const validationEditSchema = yup.object().shape({
	files: yup
		.array()
		.min(1, messages.files)
		.required(messages.files)
		.test(
			'Max Size',
			messages.maxSize,
			value =>
				!value ||
				value?.every(file => _get(file, ['size'], 0) < config.uploadFile.maxSize)
		),
	docType: yup.object().shape({
		id: yup.string().required(messages.type),
	}),
	docStatus: yup.string().required(messages.status),
	docPermissions: yup.array().when('docType', {
		is: (docType: TDocTypeSetting) => doesNeedPermission(docType),
		then: yup
			.array()
			.typeError(messages.permissions)
			.min(1, messages.permissions)
			.required(messages.permissions),
		otherwise: yup.array().nullable(),
	}),
	offerId: yup.string().when('docType', {
		is: (docType: TDocTypeSetting) => doesNeedOffer(docType),
		then: yup.string().typeError(messages.offer).required(messages.offer),
		otherwise: yup.string().nullable(),
	}),
	paUploadedDate: yup.string().when('docType', {
		is: (docType: TDocTypeSetting) => doesNeedPAUpload(docType),
		then: yup
			.string()
			.typeError(messages.paUploadedDate)
			.required(messages.paUploadedDate),
		otherwise: yup.string().nullable(),
	}),
});

export const uploadDocumentValidator = (file: File) => {
	if (file.size > config.uploadFile.maxSize) {
		return {
			message: messages.maxSize,
		} as FileError;
	}
	return null;
};
