import {
	TableBody,
	TableContainer,
	Table,
	TableRow,
	TableCell,
	MenuItem,
	Select,
	makeStyles,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import Header from './Header';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
	TJurisdictionalCostItem,
} from '../../../../../types';
import { isDisabledFee, getHoaItems, getDefaultPayor } from './helper';
import config from '../../../../../config';
import Amount from './Component/Amount';

const useStyles = makeStyles({
	cell: {
		width: '30%',
	},
	payor: {
		width: '20%',
	},
});

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	isDisabled?: boolean;
};
export const HoaTable = ({ jurisdictionalCost, isDisabled }: Props) => {
	const classes = useStyles();
	const { values, handleChange, handleBlur } =
		useFormikContext<TJurisdictionalCostTable>();

	const payorOptions = _get(jurisdictionalCost, 'PayorOptions');
	const items = _get(
		jurisdictionalCost,
		'Items'
	) as Array<TJurisdictionalCostItem>;
	const filterItems = getHoaItems(items);

	return (
		<TableContainer>
			<Table aria-labelledby='tableTitle' size='small'>
				<Header />
				<TableBody>
					{filterItems.map(item => {
						if (typeof item === 'number') return null;
						const itemKey = item.ItemKey;
						return (
							<TableRow key={itemKey}>
								<TableCell align='left' className={classes.cell}>
									{item.ItemName}
								</TableCell>
								<TableCell align='left' className={classes.cell}>
									<Amount item={item} isDisabled={isDisabled} />
								</TableCell>
								<TableCell align='left' className={classes.payor}>
									{item.Payor || config.defaultText.noValue}
								</TableCell>
								<TableCell align='left' className={classes.payor}>
									<Select
										labelId={itemKey}
										id={itemKey}
										name={itemKey}
										value={values[`${itemKey}`]}
										onChange={handleChange}
										onBlur={handleBlur}
										displayEmpty
										disabled={isDisabledFee(item) || isDisabled}
									>
										<MenuItem value=''>{getDefaultPayor(itemKey)}</MenuItem>
										{payorOptions.map(({ Payor: payor, PayorId: payorId }) => (
											<MenuItem key={payorId} value={payorId}>
												{payor}
											</MenuItem>
										))}
									</Select>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default HoaTable;
