import {
	Alert,
	Grid,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	IconButton,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import { generatePath, useHistory } from 'react-router';
import { VisibilityTwoTone } from '@material-ui/icons';
import {
	TPropertyFormDataStatuses,
	TFormCategories,
	TPropertyFormDataStatus,
} from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { getFormTypeName, getPropertyFormStatus } from '../../Helper';
import Header from '../ReviewSellerSubmission/Table/Header';
import { PATHS } from '../../../../../routes/path';

type Props = {
	propertyId: string;
	formDataStatuses: TPropertyFormDataStatuses[];
	formCategories: TFormCategories;
	title: string;
};
const AgentForms = ({
	title,
	propertyId,
	formDataStatuses,
	formCategories,
}: Props) => {
	const history = useHistory();
	const viewForm = (formId: string) => {
		const link = generatePath(PATHS.FILL_FORM_DATA, {
			itemId: propertyId,
			formId,
		});
		history.push(link);
	};

	const forms: Array<{
		id: string;
		formType: string;
		status: string;
	}> = formDataStatuses.map((status: TPropertyFormDataStatuses) => {
		const {
			FormId: formId,
			CategoryId: categoryId,
			FormTypeId: formTypeId,
		} = status;

		return {
			id: formId,
			formType: getFormTypeName(formCategories, formTypeId, categoryId),
			status: getPropertyFormStatus(status as TPropertyFormDataStatus),
		};
	});
	return (
		<SubCard title={title}>
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{forms.map(form => {
							if (typeof form === 'number') return null;
							return (
								<TableRow key={form.id}>
									<TableCell align='left'>{form.formType}</TableCell>
									<TableCell align='left'>{form.status}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton color='primary' onClick={() => viewForm(form.id)}>
											<VisibilityTwoTone sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(forms) && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
		</SubCard>
	);
};

export default AgentForms;
