import { gql, useQuery } from '@apollo/client';
import graphqlSchema from '../../../../../../graphql';
import { TUseStepHookReturn, TUseStepProps } from '../types';
import { Step1 } from './Step1';

export const useStep1 = (props: TUseStepProps): TUseStepHookReturn => {
	const { property, refetch } = props;
	const { data: listFormCategories, loading: isLoading } = useQuery(
		gql(graphqlSchema.listFormCategories)
	);

	const componentProps = {
		isLoading,
		property,
		listFormCategories,
		refetch,
	};

	return {
		render: () => <Step1 {...componentProps} />,
	};
};
