import { TPropertyFormDataStatus, TFormCategories } from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { getFormTypeName, getPropertyFormStatus } from '../../Helper';
import ReviewSellerSubmissionTable from './Table';

type Props = {
	propertyId: string;
	formStatuses: TPropertyFormDataStatus[];
	formCategories: TFormCategories;
	refetch: any;
};
const ReviewSellerSubmission = ({
	propertyId,
	formStatuses,
	formCategories,
	refetch,
}: Props) => {
	const forms: Array<{ id: string; formType: string; status: string }> =
		formStatuses.map((status: TPropertyFormDataStatus) => {
			const {
				FormId: formId,
				CategoryId: categoryId,
				FormTypeId: formTypeId,
			} = status;

			return {
				id: formId,
				formType: getFormTypeName(formCategories, formTypeId, categoryId),
				status: getPropertyFormStatus(status),
			};
		});

	return (
		<SubCard title='Review Seller Submission Modules'>
			<ReviewSellerSubmissionTable
				forms={forms}
				propertyId={propertyId}
				refetch={refetch}
			/>
		</SubCard>
	);
};

export default ReviewSellerSubmission;
