import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import { useEffect, useState } from 'react';
import graphqlSchema from '../../../../../graphql';
import { useStep1, useStep2, useStep3, useStep4 } from './index';
import { TUseStepProps, TUseStepHookReturn } from './types';

const useSteps = (
	props: TUseStepProps
): { [x: string]: TUseStepHookReturn } => {
	const step1 = useStep1(props);
	const step2 = useStep2(props);
	const step3 = useStep3(props);
	const step4 = useStep4(props);
	return {
		step1,
		step2,
		step3,
		step4,
	};
};

export const usePropertyStatus = (props: TUseStepProps) => {
	const [status, setStatus] = useState(0);
	const [error, setError] = useState('');
	const { property } = props;

	const currentStatus = _get(property, 'StatusId', 0);
	const propertyId = _get(property, 'PropertyId');

	const steps = useSteps(props);

	const [updatePropertyStatus, { loading: isUpdatingStatus }] = useMutation(
		gql(graphqlSchema.updatePropertyStatus),
		{
			onCompleted: data => {
				const newStatusId = _get(data, 'updatePropertyStatus.StatusId', 0);
				if (newStatusId) {
					setStatus(newStatusId);
				}
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				setError(message);
			},
		}
	);

	useEffect(() => {
		setStatus(currentStatus);
	}, [currentStatus]);

	const updateStatus = (status: number) => {
		updatePropertyStatus({
			variables: {
				PropertyId: propertyId,
				StatusId: status,
			},
		});
		setError('');
	};

	const currentStep = steps[`step${status}`];

	const onNext = () => {
		const validateFn = currentStep?.validate;
		if (validateFn && !validateFn()) return;

		updateStatus(status + 1);
	};

	const onBack = () => {
		updateStatus(status - 1);
	};

	return {
		status,
		onNext,
		onBack,
		isUpdatingStatus,
		error,
		clearError: () => setError(''),
		renderContent: currentStep?.render || (() => <></>),
		disabledNext: currentStep?.disabledNext,
	};
};
