import { memo, ChangeEvent } from 'react';
import _get from 'lodash.get';
import { useFormikContext } from 'formik';
import {
	Grid,
	FormHelperText,
	InputLabel,
	MenuItem,
	Select,
} from '@material-ui/core';
import { TMappedDocTypes, TEditDoc } from '../types';
import { CustomMenuProps, useSelectStyles } from './styles';

type Props = {
	docTypes: TMappedDocTypes;
};

const DocumentTypes = ({ docTypes }: Props) => {
	const classes = useSelectStyles();
	const { values, touched, errors, setFieldValue } =
		useFormikContext<TEditDoc>();

	const errorText = touched.docType && errors.docType?.id;
	const hasError = Boolean(errorText);

	const handleOnChange = (event: ChangeEvent<{ value: unknown }>) => {
		const key = event.target.value as string;
		setFieldValue('docType', _get(docTypes, key));
	};

	return (
		<Grid item xs={12}>
			<InputLabel
				htmlFor='document-type-label'
				className={classes.label}
				error={hasError}
			>
				Document type
			</InputLabel>
			<Select
				fullWidth
				labelId='document-type-label'
				id='docType'
				value={values.docType.id}
				onChange={handleOnChange}
				MenuProps={CustomMenuProps}
				error={hasError}
			>
				{Object.keys(docTypes).map(key => (
					<MenuItem key={key} value={key}>
						{_get(docTypes, [key, 'name'], '')}
					</MenuItem>
				))}
			</Select>
			<FormHelperText id='document-type-label' error={hasError}>
				{errorText}
			</FormHelperText>
		</Grid>
	);
};

export default memo(DocumentTypes);
