import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import _groupBy from 'lodash.groupby';
import { gql, useQuery, useMutation } from '@apollo/client';
import { useEffect, useState } from 'react';
import graphqlSchema from '../../../../../../graphql';
import { TUseStepHookReturn, TUseStepProps } from '../types';
import { Step3 } from './Step3';
import { TUploadFile } from '../../../../../../types';
import { useSnackBar, useUploadFiles } from '../../../../../../hooks';
import useDownloadDocs from '../../Documents/useDownloadDocs';
import {
	generateFiles,
	READY_DOC_STATUS,
	underContractUploadField,
	updateDocsCache,
} from '../../Documents/helpers';
import { APPROVED_OFFER_STEPS } from '../../../../../../helpers';

export const useStep3 = (props: TUseStepProps): TUseStepHookReturn => {
	const showSnackBar = useSnackBar();
	const { property, refetch } = props;
	const propertyId = _get(property, 'PropertyId');
	const [paDate, setPaDate] = useState('');
	const [errors, setErrors] = useState<{ [key: string]: string }>({});
	const { data: offersData, loading: isLoading } = useQuery(
		gql(graphqlSchema.getOffers),
		{
			variables: {
				PropertyId: propertyId,
				StepIds: APPROVED_OFFER_STEPS,
			},
			skip: !propertyId,
			fetchPolicy: 'no-cache',
		}
	);

	const { data: docsData, subscribeToMore } = useQuery(
		gql(graphqlSchema.getPropertyDocuments),
		{
			variables: { PropertyId: propertyId },
			skip: !propertyId,
		}
	);

	const { files, onFilesChange, uploadFiles, isUploadLoading } = useUploadFiles({
		name: 'docs_GetUploadUrls',
		query: graphqlSchema.getDocsUploadUrls,
		generateVariables: (uploadFiles: TUploadFile[]) => ({
			PropertyId: propertyId,
			OfferId: _get(offers, '0.OfferId', ''),
			DocType: underContractUploadField.docType,
			DocStatus: READY_DOC_STATUS,
			DocPermissions: [],
			FilesToUpload: uploadFiles,
		}),
		mappingFilesCallback: generateFiles,
	});

	const [updatedPAUploadedDate, { loading: isSavingPAUploadDate }] = useMutation(
		gql(graphqlSchema.updatePAUploadedDate),
		{
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const { doDownloadDocs, isDownloading } = useDownloadDocs({ propertyId });

	useEffect(() => {
		if (propertyId) {
			subscribeToMore(updateDocsCache(propertyId));
		}
	}, [propertyId]);

	useEffect(() => {
		const newErrors = { ...errors };
		if (paDate) newErrors.paDate = '';
		if (!_isEmpty(files)) newErrors.files = '';
		setErrors(newErrors);
	}, [paDate, files]);

	const paUploadedDate = _get(property, 'PAUploadedDate');
	useEffect(() => {
		setPaDate(paUploadedDate);
	}, [paUploadedDate]);

	const offers = _get(offersData, 'getOffers.Items') || [];
	const docs = _get(docsData, 'getProperty.Docs') || [];
	const docsByType = _groupBy(docs, 'DocType');
	const paDocs = docsByType[underContractUploadField.docType];

	useEffect(() => {
		onFilesChange([]);
	}, [paDocs?.length]);

	const validate = () => {
		const newErrors = {} as { [key: string]: string };
		if (!paDate) {
			newErrors.paDate = 'Acceptance Date is required';
		}
		if (_isEmpty(files) && _isEmpty(paDocs)) {
			newErrors.files = 'Signed Purchase Agreement is required';
		}

		setErrors(newErrors);
		if (!_isEmpty(newErrors)) return false;

		onSave();
		return true;
	};

	const onSave = () => {
		uploadFiles();

		if (!paDate) return;

		updatedPAUploadedDate({
			variables: {
				PropertyId: propertyId,
				PAUploadedDate: paDate,
			},
		});
	};

	const setPaUploadDate = (date: Date) => {
		setPaDate(date ? date.toISOString().slice(0, 10) : '');
	};

	const componentProps = {
		offers,
		isLoading,
		paDocs,
		refetch,
		doDownloadDocs,
		files,
		onFilesChange,
		paDate,
		setPaDate: setPaUploadDate,
		errors,
		onSave,
		isSaving: isSavingPAUploadDate || isUploadLoading,
		isDownloading,
		propertyId,
	};

	return {
		render: () => <Step3 {...componentProps} />,
		validate,
		disabledNext: _isEmpty(offers),
	};
};
