import React, { useEffect } from 'react';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
	Checkbox,
} from '@material-ui/core';
import {
	EditTwoTone as EditIcon,
	FileDownload as FileDownloadIcon,
} from '@material-ui/icons';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { TPropertyDoc, TMappedSettings, TMappedDocTypes } from '../types';
import Header from './DocumentListHeader';
import DownloadToolbar from './DownloadToolbar';
import { formatDate } from '../../../../../../helpers';
import { getHeadCells, displayDocPermissions } from './helpers';
import { useOrder, useSelectRow } from '../../../../../../hooks';
import {
	getHeadCellById,
	getComparator,
	stableSort,
} from '../../../../../../utils/table';

type Props = {
	docs: Array<TPropertyDoc>;
	handleOpenEditDialog: Function;
	handleDownloadDocs: Function;
	docStatuses: TMappedSettings;
	docPermissions: TMappedSettings;
	docTypes: TMappedDocTypes;
	isDownloading: boolean;
	isLoadingProperty: boolean;
	showPatchDownload: boolean;
	setShowPatchDownload: Function;
};

const DocumentList = ({
	docs,
	handleOpenEditDialog,
	handleDownloadDocs,
	docStatuses,
	docPermissions,
	docTypes,
	isDownloading,
	isLoadingProperty,
	showPatchDownload,
	setShowPatchDownload,
}: Props) => {
	const headCells = getHeadCells({ docStatuses, docPermissions, docTypes });
	const defaultOrder = getHeadCellById('EventTime', headCells);
	const { order, orderBy, handleRequestSort } = useOrder(defaultOrder);
	const { selected, isSelected, handleSelect, handleSelectAll } =
		useSelectRow('ObjectKey');

	useEffect(() => {
		setShowPatchDownload(Boolean(selected.length));
	}, [selected]);

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, sortedDocs);
	};

	const handleMultipleDownload = () => {
		if (_isEmpty(selected)) return;

		handleDownloadDocs(selected);
	};

	const sortedDocs = stableSort<TPropertyDoc>(
		docs,
		getComparator(order, orderBy)
	);

	return (
		<>
			{showPatchDownload && (
				<DownloadToolbar
					handleMultipleDownload={handleMultipleDownload}
					isDownloading={isDownloading}
				/>
			)}
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<Header
						order={order}
						orderBy={orderBy}
						onRequestSort={handleRequestSort}
						numSelected={selected.length}
						onSelectAllClick={handleSelectAllClick}
						rowCount={sortedDocs.length}
						headCells={headCells}
					/>
					<TableBody>
						{sortedDocs.map((doc, index) => {
							const isItemSelected = isSelected(doc.ObjectKey);
							const labelId = `enhanced-table-checkbox-${index}`;
							return (
								<TableRow
									hover
									tabIndex={-1}
									key={doc.ObjectKey}
									onClick={() => handleSelect(doc.ObjectKey)}
									role='checkbox'
									aria-checked={isItemSelected}
									selected={isItemSelected}
								>
									<TableCell sx={{ pl: 3 }} padding='checkbox'>
										<Checkbox
											color='primary'
											checked={isItemSelected}
											inputProps={{
												'aria-labelledby': labelId,
											}}
										/>
									</TableCell>
									<TableCell align='left'>{_get(doc, ['File', 'name'])}</TableCell>
									<TableCell align='left'>
										{_get(docTypes, [doc.DocType, 'name'], '')}
									</TableCell>
									<TableCell align='left'>{docStatuses[doc.DocStatus]}</TableCell>
									<TableCell align='left'>
										{displayDocPermissions(docPermissions, doc.DocPermissions)}
									</TableCell>
									<TableCell align='left'>{doc.OfferNo}</TableCell>
									<TableCell align='left'>{formatDate(doc.EventTime)}</TableCell>
									<TableCell align='center' sx={{ width: '10%' }}>
										<IconButton
											color='primary'
											onClick={() => {
												handleOpenEditDialog(doc);
											}}
										>
											<EditIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
										<IconButton
											color='primary'
											onClick={() => handleDownloadDocs([doc.ObjectKey])}
											disabled={isDownloading}
										>
											<FileDownloadIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{!isLoadingProperty && _isEmpty(docs) && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
		</>
	);
};

export default DocumentList;
