import {
	Grid,
	Button,
	CircularProgress,
	Box,
	Divider,
	FormLabel,
	Typography,
	makeStyles,
} from '@material-ui/core';
import { useState, useEffect } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import _groupBy from 'lodash.groupby';
import { useQuery, gql } from '@apollo/client';
import SubCard from '../../../../../../ui-component/cards/SubCard';
import UploadFile from '../../../../components/UploadFile';
import config from '../../../../../../config';
import { addPreviewProp } from '../../../../../../helpers';
import useUploadMultipleDocs from '../useUploadMultipleDocs';
import { TDocTypeSetting, TEditDoc, TFile } from '../types';
import { uploadDocumentValidator } from '../DocumentDialog/validation';
import Loader from '../../../../../../ui-component/Loader';
import {
	disclosureUploadFields,
	REVIEWING_DOC_STATUS,
	updateDocsCache,
	isTransactionDocument,
} from '../helpers';
import graphqlSchema from '../../../../../../graphql';
import PreviewUploadedDoc from '../PreviewUploadedDoc';
import useDownloadDocs from '../useDownloadDocs';

const useStyles = makeStyles(theme => ({
	required: {
		color: theme.palette.error.main,
	},
}));

type Props = {
	propertyId: string;
	offerId: string;
};

const DisclosureDocuments = ({ propertyId, offerId }: Props) => {
	const classes = useStyles();
	const [files, setFiles] = useState<{ [key: string]: TFile[] }>({});
	const { data, subscribeToMore } = useQuery(
		gql(graphqlSchema.getPropertyDocuments),
		{
			variables: { PropertyId: propertyId },
			skip: !propertyId,
		}
	);
	const { uploadFiles, isUploadLoading } = useUploadMultipleDocs({
		propertyId,
	});

	const { doDownloadDocs, isDownloading } = useDownloadDocs({ propertyId });

	useEffect(() => {
		if (propertyId) {
			subscribeToMore(updateDocsCache(propertyId));
		}
	}, []);

	useEffect(() => {
		setFiles({});
	}, [data]);

	const docs = _get(data, 'getProperty.Docs') || [];
	const onboardingDocs = _groupBy(docs, 'DocType');

	const onSave = () => {
		const values = Object.entries(files).reduce(
			(result: TEditDoc[], [docType, docFiles]: [string, TFile[]]) => {
				if (_isEmpty(docFiles)) return result;
				const doc = {
					docType: { id: docType } as TDocTypeSetting,
					docStatus: REVIEWING_DOC_STATUS,
					files: docFiles,
					...(isTransactionDocument(docType) && { offerId }),
				} as TEditDoc;
				return [...result, doc];
			},
			[]
		);
		if (!_isEmpty(values)) uploadFiles(values);
	};

	if (!data) return <></>;

	return (
		<SubCard title='Disclosures'>
			{(isUploadLoading || isDownloading) && <Loader />}
			<Grid container width='full'>
				{disclosureUploadFields.map(({ docType, name, isRequired }) => (
					<Grid key={docType} item container xs={12} sx={{ p: 1, pb: 2 }}>
						<Grid item container lg={5} xs={12} alignItems='center'>
							<FormLabel
								required={isRequired}
								classes={{ asterisk: classes.required }}
							>
								<Typography variant='h5' sx={{ display: 'inline-block' }}>
									{name}
								</Typography>
							</FormLabel>
						</Grid>
						<Grid item lg={7} xs={12}>
							<Box sx={{ mb: 2 }}>
								{(onboardingDocs[docType] || []).map(doc => (
									<PreviewUploadedDoc
										key={`${docType}_${doc.File.name}`}
										file={doc.File}
										onDownload={() => doDownloadDocs(doc.ObjectKey)}
									/>
								))}
							</Box>
							<UploadFile
								name='docs'
								files={files[docType] || []}
								onDrop={droppedFiles => {
									setFiles({ ...files, [docType]: addPreviewProp(droppedFiles) });
								}}
								accept={config.uploadFile.acceptedTypes}
								validator={uploadDocumentValidator}
							/>
						</Grid>
						<Grid item xs={12}>
							<Divider sx={{ py: 1 }} />
						</Grid>
					</Grid>
				))}
			</Grid>
			<Grid container width='full' justifyContent='flex-end'>
				<Button
					sx={{ mx: 1 }}
					onClick={onSave}
					variant='contained'
					color='primary'
					disabled={isUploadLoading}
				>
					{isUploadLoading ? (
						<CircularProgress size='1.5rem' color='secondary' />
					) : (
						'Save'
					)}
				</Button>
			</Grid>
		</SubCard>
	);
};

export default DisclosureDocuments;
