import _get from 'lodash.get';
import { HeadCell } from '../../../../../../types';
import { TPropertyDoc, TMappedDocTypes, TMappedSettings } from '../types';

export const getHeadCells = ({
	docTypes,
	docStatuses,
	docPermissions,
}: {
	docTypes: TMappedDocTypes;
	docStatuses: TMappedSettings;
	docPermissions: TMappedSettings;
}): HeadCell<TPropertyDoc>[] => [
	{
		id: 'File.name',
		numeric: false,
		label: 'Document Name',
		align: 'left',
		disablePadding: false,
	},
	{
		id: 'DocType.name',
		numeric: false,
		label: 'Type',
		align: 'left',
		disablePadding: false,
		pathFn: doc => _get(docTypes, [doc.DocType, 'name'], ''),
	},
	{
		id: 'DocStatus',
		numeric: false,
		label: 'Status',
		align: 'left',
		pathFn: doc => _get(docStatuses, [doc.DocStatus], ''),
	},
	{
		id: 'DocPermissions',
		numeric: false,
		label: 'Permissions',
		align: 'left',
		pathFn: doc => displayDocPermissions(docPermissions, doc.DocPermissions),
	},
	{
		id: 'OfferNo',
		numeric: false,
		label: 'Offer No',
		align: 'left',
	},
	{
		id: 'EventTime',
		numeric: false,
		label: 'Created Date',
		align: 'left',
	},
];

export const displayDocPermissions = (
	docPermissions: TMappedSettings,
	values: string[]
) =>
	values.reduce(
		(result, permission) =>
			result
				? `${result}, ${docPermissions[permission]}`
				: `${docPermissions[permission]}`,
		''
	);
