import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { TreeView, TreeItem } from '@material-ui/lab';
import { makeStyles, LinearProgress } from '@material-ui/core';
import { useState, SyntheticEvent } from 'react';
import {
	ExpandMore as ExpandMoreIcon,
	ChevronRight as ChevronRightIcon,
	CheckCircleOutlined as CheckIcon,
	CircleOutlined as UnCheckIcon,
} from '@material-ui/icons';
import { generatePath, useHistory } from 'react-router';
import graphSchema from '../../../../../graphql';
import { TShortForm, TPropertyFormDataStatus } from '../../../../../types';
import { PATHS } from '../../../../../routes/path';
import { getFormTypeName, isFormSubmitted } from '../../Helper';
import { reorderCategories } from '../../../../../helpers';

type Props = {
	propertyId: string;
	formStatuses: TPropertyFormDataStatus[];
};

const useStyles = makeStyles({
	root: {
		height: 'auto',
		flexGrow: 1,
	},
});

const FormList = ({ propertyId, formStatuses }: Props) => {
	const classes = useStyles();
	const history = useHistory();
	const { loading, data } = useQuery(gql(graphSchema.getPropertyForms), {
		variables: {
			PropertyId: propertyId,
		},
	});
	const { loading: isFormTypeLoading, data: formCategories } = useQuery(
		gql(graphSchema.listFormCategories)
	);
	const [expanded, setExpanded] = useState<string[]>([]);
	const handleToggle = (event: SyntheticEvent, nodeIds: string[]) => {
		setExpanded(nodeIds);
	};

	const [selected, setSelected] = useState<string[]>([]);
	const handleSelect = (event: SyntheticEvent, nodeIds: string[]) => {
		setSelected(nodeIds);
	};
	const onClickHandler = (propertyId: string, formId: string) => {
		const formViewLink = generatePath(PATHS.PROPERTY_FORM, {
			propertyId,
			formId,
		});
		history.push(formViewLink);
	};
	const forms = _get(data, 'formdata_GetPropertyFormDataList', {});
	const listFormCategories = _get(formCategories, 'listFormCategories');
	const categories = reorderCategories(listFormCategories);
	if (loading || isFormTypeLoading) return <LinearProgress />;
	if (_isEmpty(forms)) return <></>;

	return (
		<TreeView
			className={classes.root}
			defaultCollapseIcon={<ExpandMoreIcon />}
			defaultExpandIcon={<ChevronRightIcon />}
			expanded={expanded}
			selected={selected}
			onNodeToggle={handleToggle}
			onNodeSelect={handleSelect}
		>
			<TreeItem nodeId='1' label='See Onboarding Progress'>
				{forms.map((form: TShortForm) => (
					<TreeItem
						key={form.FormId}
						nodeId={form.FormId}
						label={getFormTypeName(categories, form.FormTypeId, form.CategoryId)}
						onClick={() => onClickHandler(propertyId, form.FormId)}
						icon={
							isFormSubmitted(formStatuses, form.FormTypeId) ? (
								<CheckIcon color='primary' />
							) : (
								<UnCheckIcon />
							)
						}
					/>
				))}
			</TreeItem>
		</TreeView>
	);
};

export default FormList;
