import { List, ListItem, ListItemText, Typography } from '@material-ui/core';
import _get from 'lodash.get';
import TextItem from '../../../components/TextItem';
import { TProperty } from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { PropertyExternalUrl } from './PropertyExternalUrl';
import { CalendarUrl } from './CalendarUrl';
import {
	convertToFile,
	formatCurrency,
	formatDate,
} from '../../../../../helpers';
import Preview from '../../../components/UploadFile/Preview';
import config from '../../../../../config';

type Props = {
	property: TProperty;
};

export const ListingInformation = ({ property }: Props) => {
	const externalId = _get(property, 'ExternalId', '');
	const statusId = +_get(property, 'StatusId', 0);
	const thumbnail = _get(property, 'Thumbnail', '');
	const calendarUrl = _get(property, 'CalendarUrl', '');
	const streetAddress = _get(property, ['PropertyData', 'StreetAddress'], '') || '';
	const getListPrice = (row: TProperty) => {
		const price = _get(row, ['ListingDetails', 'ListPrice']);
		return price ? formatCurrency(price) : config.defaultText.noValue;
	};

	return (
		<SubCard title='Listing Information'>
			<List>
				<TextItem<TProperty>
					data={property}
					label='Approved Listing Price'
					getValue={getListPrice}
				/>
				<TextItem<TProperty>
					data={property}
					label='Buyer Agent Fee'
					getValue={row => `${_get(row, 'BuyerAgentFeePct')}%`}
				/>
				<TextItem<TProperty>
					data={property}
					label='Listing Agent Fee'
					getValue={row => `${_get(row, 'ListingAgentFeePct')}%`}
				/>
				<TextItem<TProperty>
					data={property}
					label='Listing Termination Date'
					getValue={row => formatDate(_get(row, 'ListingTerminationDate'))}
				/>
				<TextItem<TProperty>
					data={property}
					label='Offer Due Date'
					getValue={row => formatDate(_get(row, 'OfferDueDate'))}
				/>
				<CalendarUrl url={calendarUrl} />
				<TextItem<TProperty>
					data={property}
					source='ListingTermsConditions'
					label='Special Listing Terms & Conditions'
					labelSx={{ minWidth: { xs: '50%', sm: 250 } }}
					contentSx={{ textAlign: 'right' }}
				/>
				<TextItem<TProperty>
					data={property}
					source='AdditionalInfo'
					label='Property Description'
					labelSx={{ minWidth: { xs: '50%', sm: 200 } }}
					contentSx={{ textAlign: 'right' }}
				/>
				<ListItem sx={{ px: 0, py: 1 }}>
					<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
						<Typography variant='h5'>Photo</Typography>
					</ListItemText>
					{Boolean(thumbnail) && <Preview file={convertToFile(thumbnail)} />}
				</ListItem>
				<ListItem sx={{ px: 0, py: 1 }}>
					<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
						<Typography variant='h5'>Listed Property URL</Typography>
					</ListItemText>
					<PropertyExternalUrl
						externalId={externalId}
						status={statusId}
						streetAddress={streetAddress}
					/>
				</ListItem>
			</List>
		</SubCard>
	);
};
