import { Grid, Button, FormHelperText } from '@material-ui/core';
import { FileDownload as FileDownloadIcon } from '@material-ui/icons';
import { useFormikContext } from 'formik';
import { TEditDoc } from '../types';
import { gridSpacing } from '../../../../../../store/constant';
import { TFile } from '../../../../../../types';
import UploadFile from '../../../../components/UploadFile';
import config from '../../../../../../config';

type Props = {
	isEditing: boolean;
	isDownloading: boolean;
	isUploadLoading: boolean;
	handleDownloadDocs: Function;
	objectKey: string;
	files: Array<TFile>;
	onFilesChange: Function;
};

const DocumentFiles = ({
	isEditing,
	isDownloading,
	handleDownloadDocs,
	objectKey,
	files,
	onFilesChange,
	isUploadLoading,
}: Props) => {
	const { touched, errors, setFieldValue } = useFormikContext<TEditDoc>();

	if (isEditing) {
		return (
			<Grid item xs={12}>
				<Button
					variant='outlined'
					startIcon={<FileDownloadIcon />}
					sx={{ textTransform: 'none' }}
					disabled={isDownloading}
					onClick={() => {
						handleDownloadDocs(objectKey);
					}}
				>
					Download document
				</Button>
			</Grid>
		);
	}

	const errorText = touched.files && errors.files;
	const hasError = Boolean(errorText);

	return (
		<Grid item xs={12}>
			<Grid
				container
				spacing={gridSpacing}
				alignItems='flex-start'
				justifyContent='space-between'
				direction='row'
			>
				<Grid item xs={12}>
					<UploadFile
						name='docs'
						files={files}
						onDrop={files => {
							setFieldValue('files', files);
							onFilesChange(files);
						}}
						loading={isUploadLoading}
						accept={config.uploadFile.acceptedTypes}
						hasError={hasError}
					/>
				</Grid>
				<Grid item xs={12}>
					<FormHelperText error={hasError}>{errorText}</FormHelperText>
				</Grid>
			</Grid>
		</Grid>
	);
};

export default DocumentFiles;
