import * as yup from 'yup';
import { AMOUNT_TYPES } from './helper';

export const validationAddHoa = yup.object().shape({
	PrivateTransferFeeAmount: yup.number().when('hoaDetectedOverride', {
		is: 'Yes',
		then: yup.number().when('PrivateTransferFeeAmountType', {
			is: AMOUNT_TYPES.percentage.value,
			then: yup
				.number()
				.typeError('Amount must be a number type')
				.required('Amount is a required field')
				.min(0.01, 'Amount must be greater than or equal to 0.01')
				.max(100, 'Amount must be less than or equal to 100'),
			otherwise: yup.number().typeError('Amount must be a number type').required('Amount is a required field'),
		}),
	}),
	PrivateTransferFeeAmountType: yup
		.string()
		.ensure()
		.when('hoaDetectedOverride', {
			is: 'Yes',
			then: yup.string().required('Amount type is a required field'),
		}),
});
