import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	HeadCell,
	TJurisdictionalCostItem,
	TPropertyFormDataStatus,
} from '../../../../../types';
import config from '../../../../../config';
import { isYes } from '../../../../../helpers';

const CALC_VALUE = 'calc';
const CALC_TEXT = 'Calculated based on Listing Price';
export const PRIVATE_TRANSFER_FEE = 'PrivateTransferFee';
const HOA_ITEMS = ['HOATransferFee', 'HOADocuments', PRIVATE_TRANSFER_FEE];
const DISABLED_FEES = [HOA_ITEMS[0], HOA_ITEMS[1]];
const LISTING_FORM_TYPE_ID = 'ld';
export const AMOUNT_TYPES = {
	amount: { value: 'Amount', label: '$ Amount' },
	percentage: { value: 'Percentage', label: 'Percentage' },
};
const DEFAULT_PAYOR = 'Customary Terms';
const DEFAULT_PRIVATE_TRANSFER_FEE_PAYOR = 'No Fee';

export const DEAFAULT_AMOUNT_PLACEHOLDER = {
	Amount: '$ Amount',
	Percentage: 'Percentage',
}

export const headCells: HeadCell<TJurisdictionalCostItem>[] = [
	{
		id: 'ItemName',
		numeric: false,
		disablePadding: false,
		label: 'Charges',
	},
	{
		id: 'PayAmount',
		numeric: false,
		disablePadding: false,
		label: 'Amount* \n(Based on offer price)',
	},
	{
		id: 'Payor',
		numeric: false,
		disablePadding: false,
		label: 'Customary Pays',
	},
];

export const getPayAmount = (value: string) => {
	if (_isEmpty(value)) return '';
	if (value.toLowerCase() === CALC_VALUE) return CALC_TEXT;
	return value;
};

export const isDisabledFee = (item: TJurisdictionalCostItem) =>
	DISABLED_FEES.includes(item.ItemKey) ||
	item.Payor === config.defaultText.noValue ||
	item.Payor === config.defaultText.noneValue;

export const getItemsKeys = (jurisdictionalCost: TJurisdictionalCostItem[]) =>
	jurisdictionalCost.reduce<string[]>((values, jurisdiction) => {
		const itemKey = _get(jurisdiction, 'ItemKey');
		if (HOA_ITEMS.includes(itemKey)) {
			return [...values];
		}
		return [...values, itemKey];
	}, []);

export const prepareInitValue = (
	jurisdictionalCost: TJurisdictionalCostItem[]
) =>
	jurisdictionalCost.reduce<{ [key: string]: string }>(
		(values, jurisdiction) => {
			const itemKey = _get(jurisdiction, 'ItemKey');
			const value = _get(jurisdiction, 'PayorIdOverride') || '';
			if (itemKey === PRIVATE_TRANSFER_FEE) {
				const itemAmountKey = `${itemKey}Amount`;
				const amount = _get(jurisdiction, ['PayAmountOverride', 'Amount']) || 0;
				const itemAmountTypeKey = `${itemKey}AmountType`;
				const amountType =
					_get(jurisdiction, ['PayAmountOverride', 'AmountType']) || 'Amount';
				return {
					...values,
					[`${itemKey}`]: value,
					[`${itemAmountKey}`]: amount,
					[`${itemAmountTypeKey}`]: amountType,
				};
			}
			return { ...values, [`${itemKey}`]: value };
		},
		{}
	);

export const isFormListingSubmitted = (
	propertyFormDataStatuses: TPropertyFormDataStatus[]
) =>
	propertyFormDataStatuses.some(
		form => form.FormTypeId === LISTING_FORM_TYPE_ID && form.FormDataSubmitted
	);

export const getGeneralItems = (items: TJurisdictionalCostItem[]) =>
	items.filter(item => !HOA_ITEMS.includes(item.ItemKey));
export const getHoaItems = (items: TJurisdictionalCostItem[]) =>
	items.filter(item => HOA_ITEMS.includes(item.ItemKey));

export const convertBooleanToYesNo = (item: boolean | null) => {
	if (item) {
		return 'Yes';
	}
	if (item === false) {
		return 'No';
	}
	return '';
};

export const prepareValues = (
	values: { [key: string]: string | boolean },
	jurisdictionalCostItems: Array<TJurisdictionalCostItem>,
	propertyId: string
) => {
	const itemKeys = getItemsKeys(jurisdictionalCostItems);
	const payors = itemKeys.reduce<Array<{ [key: string]: string | boolean }>>(
		(lastPayors, itemKey) => {
			const payorId = _get(values, itemKey);
			if (!payorId) return [...lastPayors];
			return [
				...lastPayors,
				{
					PayorId: payorId,
					ItemKey: itemKey,
				},
			];
		},
		[]
	);
	const hoaDetectedOverride = _isEmpty(_get(values, 'hoaDetectedOverride'))
		? ''
		: isYes(String(_get(values, 'hoaDetectedOverride'))) || false;

	const privateTransferFeePayor = _get(values, PRIVATE_TRANSFER_FEE, '');
	const privateTransferFeeAmount = {
		ItemKey: PRIVATE_TRANSFER_FEE,
		PayAmount: {
			Amount: Number(_get(values, 'PrivateTransferFeeAmount', 0)),
			AmountType: _get(values, 'PrivateTransferFeeAmountType', ''),
		},
		...(privateTransferFeePayor && {
			PayorId: _get(values, PRIVATE_TRANSFER_FEE, ''),
		}),
	};
	const paymentOverride = [
		...payors,
		hoaDetectedOverride && {
			...privateTransferFeeAmount,
		},
	].filter(item => !_isEmpty(_get(item, 'ItemKey')));
	const variables = {
		HOADetectedOverride: hoaDetectedOverride,
		PropertyId: propertyId,
		PaymentOverride: paymentOverride,
	};
	return variables;
};

export const getDefaultPayor = (item: string) => item === PRIVATE_TRANSFER_FEE
		? DEFAULT_PRIVATE_TRANSFER_FEE_PAYOR
		: DEFAULT_PAYOR;
