import {
	MenuItem,
	Select,
	TableBody,
	makeStyles,
	TableContainer,
	Table,
	TableCell,
	TableRow,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
} from '../../../../../types';
import { isYes } from '../../../../../helpers';
import HoaTable from './HoaTable';

const useStyles = makeStyles({
	hoa: {
		paddingLeft: '0px !important',
	},
	cell: {
		width: '30%',
	},
	payor: {
		width: '20%',
		paddingLeft: '0px',
	},
});

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	isDisabled?: boolean;
};

export const Hoa = ({ jurisdictionalCost, isDisabled }: Props) => {
	const classes = useStyles();
	const { values, handleChange } = useFormikContext<TJurisdictionalCostTable>();
	const isDisplay = isYes(values.hoaDetectedOverride) || false;

	return (
		<>
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<TableBody>
						<TableRow>
							<TableCell align='left' className={classes.cell}>
								HOA Fees
							</TableCell>
							<TableCell align='left' className={classes.cell} />
							<TableCell align='left' className={classes.payor} />
							<TableCell align='left' className={classes.cell}>
								<Select
									labelId='hoaDetectedOverride'
									id='hoaDetectedOverride'
									value={values.hoaDetectedOverride}
									label='hoaDetectedOverride'
									onChange={handleChange}
									name='hoaDetectedOverride'
									displayEmpty
									disabled={isDisabled}
								>
									<MenuItem value='No'>No</MenuItem>
									<MenuItem value='Yes'>Yes</MenuItem>
								</Select>
							</TableCell>
						</TableRow>
					</TableBody>
				</Table>
			</TableContainer>
			{isDisplay && (
				<HoaTable jurisdictionalCost={jurisdictionalCost} isDisabled={isDisabled} />
			)}
		</>
	);
};

export default Hoa;
