import { memo, useEffect, useState } from 'react';
import { useFormikContext } from 'formik';
import {
	Grid,
	FormHelperText,
	InputLabel,
	MenuItem,
	Select,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import { TMappedSettings, TEditDoc } from '../types';
import { CustomMenuProps, useSelectStyles } from './styles';

type Props = {
	docPermissions: TMappedSettings;
};

const DocumentPermissions = ({ docPermissions }: Props) => {
	const classes = useSelectStyles();
	const [validPermissions, setValidPermissions] = useState<TMappedSettings>({});
	const { values, handleChange, touched, errors } = useFormikContext<TEditDoc>();

	useEffect(() => {
		const mapValidPermissions = values.docType.availablePermissions.reduce(
			(result, permission) => ({
				...result,
				[permission]: docPermissions[permission],
			}),
			{}
		);
		setValidPermissions(mapValidPermissions);
	}, [values.docType.id]);

	const errorText = touched.docPermissions && errors.docPermissions;
	const hasError = Boolean(errorText);

	if (_isEmpty(validPermissions)) return <></>;

	return (
		<Grid item xs={12}>
			<InputLabel
				htmlFor='document-permission-label'
				className={classes.label}
				error={hasError}
			>
				Document permissions
			</InputLabel>
			<Select
				fullWidth
				multiple
				labelId='document-permission-label'
				id='docPermissions'
				value={values.docPermissions}
				onChange={handleChange('docPermissions')}
				error={hasError}
				MenuProps={CustomMenuProps}
			>
				{Object.keys(validPermissions).map(permission => (
					<MenuItem key={permission} value={permission}>
						{validPermissions[permission]}
					</MenuItem>
				))}
			</Select>
			<FormHelperText id='document-permission-label' error={hasError}>
				{errorText}
			</FormHelperText>
		</Grid>
	);
};

export default memo(DocumentPermissions);
