import { Formik } from 'formik';
import { Grid, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { TSearchFilters, TDocSetting, TDocTypeSetting } from '../types';
import DocumentSearch from './DocumentSearch';
import DocumentFilter from './DocumentFilter';

const initSearchFiltersValue = {
	search: '',
	docTypes: [],
	docStatuses: [],
	docPermissions: [],
};

const useStyles = makeStyles(() => ({
	form: {
		width: '100%',
	},
}));

type Props = {
	handleSearchFilters: Function;
	handleResetSearchFilters: Function;
	docStatuses: TDocSetting[];
	docPermissions: TDocSetting[];
	docTypes: TDocTypeSetting[];
};

const DocumentSearchFilters = ({
	handleSearchFilters,
	handleResetSearchFilters,
	docTypes,
	docStatuses,
	docPermissions,
}: Props) => {
	const classes = useStyles();
	const onSubmitHandler = (values: TSearchFilters) => {
		handleSearchFilters(values);
	};

	const onResetSearchFilters = (resetForm: Function) => {
		resetForm();
		handleResetSearchFilters();
	};

	return (
		<Formik
			initialValues={initSearchFiltersValue}
			onSubmit={onSubmitHandler}
			enableReinitialize
		>
			{({ handleSubmit, resetForm }) => (
				<form noValidate onSubmit={handleSubmit} className={classes.form}>
					<Grid
						container
						direction='row'
						justifyContent='flex-start'
						alignItems='center'
						spacing={1}
					>
						<Grid item xs={12} lg={4}>
							<DocumentSearch />
						</Grid>
						<Grid item xs={12} lg={4}>
							<DocumentFilter
								tags={docTypes}
								fieldName='docTypes'
								placeholder='Types'
							/>
						</Grid>
						<Grid item xs={12} lg={4}>
							<DocumentFilter
								tags={docStatuses}
								fieldName='docStatuses'
								placeholder='Statuses'
							/>
						</Grid>
						<Grid item xs={12} lg={4}>
							<DocumentFilter
								tags={docPermissions}
								fieldName='docPermissions'
								placeholder='Permissions'
							/>
						</Grid>
						<Grid item xs={12} lg={1}>
							<Button
								type='submit'
								variant='contained'
								color='primary'
								size='large'
								fullWidth
							>
								Search
							</Button>
						</Grid>
						<Grid item xs={12} lg={1}>
							<Button
								type='button'
								variant='outlined'
								size='large'
								fullWidth
								onClick={() => onResetSearchFilters(resetForm)}
							>
								Reset
							</Button>
						</Grid>
					</Grid>
				</form>
			)}
		</Formik>
	);
};

export default DocumentSearchFilters;
