import {
	Link,
	makeStyles,
	ListItem,
	ListItemText,
	Typography,
} from '@material-ui/core';
import { CopyToClipboard } from '../../../components/CopyToClipboard';
import TextItem from '../../../components/TextItem';

const useStyles = makeStyles(() => ({
	root: {
		maxWidth: '70%',
	},
}));

type Props = {
	url: string;
};

export const CalendarUrl = ({ url }: Props) => {
	const classes = useStyles();

	if (!url)
		return (
			<TextItem<Object>
				data={{}}
				source=''
				label='Calendar URL'
				labelSx={{ minWidth: { xs: '50%', sm: 200 } }}
				contentSx={{ textAlign: 'right' }}
			/>
		);

	return (
		<ListItem sx={{ px: 0, py: 1 }}>
			<ListItemText sx={{ minWidth: { xs: '50%', sm: 'auto' } }}>
				<Typography variant='h5'>Calendar URL</Typography>
			</ListItemText>
			<div className={classes.root}>
				<CopyToClipboard
					content={url}
					show={
						<Link
							color='primary'
							variant='body1'
							href={url}
							target='_blank'
							rel='noopener noreferrer'
						>
							{url}
						</Link>
					}
				/>
			</div>
		</ListItem>
	);
};
